import ReactDOM from 'react-dom';
import React, { Component } from 'react'

import * as d3 from "d3"




var colors=['#f700ff','#fbb132','#0885c2','#001aff','#7b28b7','#1c8b3c','#ed334e']

class DataHacker extends Component{
    constructor() {
        super();
        this.globalCategory=0
        this.points=[]
        this.numOfPoints=0
        this.nowCategory=0
        this.jsonoutput=null
        this.input=null
        this.width=60
        this.height=70
    }

    componentDidMount() {
        d3.select("svg")
            .on("click",this.leftClick.bind(this))
/*        for (let i=0;i<11;i++){
            d3.select("svg")
                .append("line")
                .attr("x1",(i+1)*1000/11)
                .attr("y1",0)
                .attr("x2",(i+1)*1000/11)
                .attr("y2",1000)
                .attr("stroke", "black")
                .attr("stroke-width", "2px");

        }*/

    }


    drawPoint(x,y,color,cat,index){
        d3.select("svg")
            .append("circle")
            .attr("cx",x)
            .attr("cy",y)
            .attr("r",7)
            .attr("fill",color)
            .attr("catefory",cat)
            .attr("index",index)
            .on("click",this.deletePoint.bind(this))
    }

    drawRect(x,y,w,h,color,cat,index){
        d3.select("svg")
            .append("rect")
            .attr("x",x-w/2)
            .attr("y",y-h/2)
            .attr("width",w)
            .attr("height",h)
            .attr("fill",color)
            .attr("catefory",cat)
            .attr("index",index)
            .on("click",this.deletePoint.bind(this))
        d3.select("svg")
            .append("text")
            .attr("x",x-w/2)
            .attr("y",y-h/2)
            .attr("index",index)
            .style("font-size",12)
            .text(index)
    }

    deletePoint(e){
        e.stopPropagation()
        let el=e.target
        let index=d3.select(el).attr("index")
        this.points[index]=-1
        d3.select(el).remove()
        console.log(index)
        d3.selectAll("text")
            .filter(function(d,i){
                return d3.select(this).attr("index")==index
            })
            .remove()
        // console.log(this.points)
    }

    leftClick(e){
        // console.log(e)
        let x=e.offsetX
        let y=e.offsetY
        // this.drawRect(x,y,this.width,this.height,colors[this.nowCategory],this.nowCategory,this.numOfPoints)
        this.drawPoint(x,y,colors[this.nowCategory],this.nowCategory,this.numOfPoints)
        this.points.push({
            x:x,
            y:y,
            cat:this.nowCategory,
            id:this.numOfPoints,
            radius:7
            // width:this.width,
            // height:this.height
        })
        this.numOfPoints+=1
        // console.log(this.points)
    }

    savePoints(){
        let tempPoints=[]
        let num=0
        for (let i=0;i<this.points.length;i++){
            if(this.points[i]!=-1){
                this.points[i].id=num
                num+=1
                tempPoints.push(this.points[i])
            }
        }
        this.points=tempPoints
        d3.select("svg").selectAll("circle").remove()
        d3.select("svg").selectAll("text").remove()

        for (let i=0;i<this.points.length;i++){
            // this.drawRect(this.points[i].x,this.points[i].y,this.points[i].width,this.points[i].height,colors[this.points[i].cat],this.points[i].cat,i)
            this.drawPoint(this.points[i].x,this.points[i].y,colors[this.points[i].cat],this.points[i].cat,i)

        }
        this.numOfPoints=this.points.length

        for (let i=0;i<this.points.length;i++){
            this.points[i].cat=this.points[i].cat.toString()
            this.points[i].shape="circle"

        }

        // console.log(this.points)
        let res=JSON.stringify(this.points)
        this.jsonoutput.innerHTML=res
        console.log(res)
    }

    addCategory(){
        this.globalCategory+=1
        this.nowCategory=this.globalCategory
        console.log(this.globalCategory)
    }

    loadPoints(){
        fetch('/data/savedDate.txt',{
            method:'GET',
            headers:{
                'Content-Type':'application/json;charset=UTF-8'
            },
            mode:'cors',
            cache:'default'
        })
            .then(res=>res.json())
            .then(data=>{
                console.log(data)
                this.points=data
                this.numOfPoints=this.points.length-1
                let categories=[]

                for (let i=0;i<this.points.length;i++){
                    this.drawPoint(this.points[i].x,this.points[i].y,colors[this.points[i].cat],this.points[i].cat,i)
                    if(categories.indexOf(this.points[i].cat)<0){
                        categories.push(this.points[i].cat)
                    }
                }
                this.globalCategory=categories.length-1
                 this.nowCategory=this.globalCategory
                console.log(this.points)
            })
    }

    switchCategory(){
        let cat=parseInt(this.input.value)
        this.nowCategory=cat
        // this.input.value=""
        console.log(cat)
    }
    changeWidthHeight(){
        let wh=this.input2.value.split(",")
        console.log(this.input.value)
        let w=parseFloat(wh[0])
        let h=parseFloat(wh[1])
        this.width=w
        this.height=h
        // this.input.value=""
        console.log(w,h)
    }

    render(){
        return(
            <div>
                <svg id={"hacksvg"} width={"1000px"} height={"1000px"} >
                </svg>
                <button onClick={this.addCategory.bind(this)}  style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1550px",bottom:"800px"}}>Add Category</button>
                <input ref={(input)=>this.input=input}  style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1550px",bottom:"750px"}}></input>
                <button onClick={this.switchCategory.bind(this)}  style={{width:200,height:40,fontSize:"20px",position:"absolute",left:"1710px",bottom:"750px",marginLeft:10}}>Change Category</button>
                <button onClick={this.savePoints.bind(this)} style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1550px",bottom:"700px",marginLeft:10}}>Save</button>
                <button onClick={this.loadPoints.bind(this)} style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1710px",bottom:"700px",marginLeft:10}}>Load</button>
                <input ref={(input)=>this.input2=input}  style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1550px",bottom:"650px"}}></input>
                <button onClick={this.changeWidthHeight.bind(this)} style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1710px",bottom:"650px",marginLeft:10}}>Change Width and Height</button>
                <label ref={(jsonoutput)=>this.jsonoutput=jsonoutput} style={{width:300,height:200,fontSize:"10px",position:"absolute",left:"1550px",bottom:"300px",marginLeft:10,overflow:"scroll"}}>

                </label>
            </div>)
    }
}

export default DataHacker