    addEdge(index1,index2,cat){
        let that=this
        let newedge={
            start:index1,
            end:index2,
            weight:-1,
            cat:cat,
            dis:-1,
            virtrual:false,
            virtrualNodes:[],
            id:this.edges.length,
            isLocalTree:false
        }
        newedge=this.surfaceRoutingEdge(newedge)
        this.edges.push(newedge)

        this.drawOneEdge(newedge.id)
    }

