    scaleData(){

        let minX = Number.MAX_VALUE
        let minY = Number.MAX_VALUE
        let maxX = Number.MIN_VALUE
        let maxY = Number.MIN_VALUE
        let scale_ratio_x = 1
        let scale_ratio_y = 1
        let transform_dx = 0
        let transform_dy = 0

        if(this.pointData.length <= 0){
            return
        }

        if(this.pointData[0].shape == "rectangle"){
            for(let i = 0; i < this.pointData.length; i ++){
                let x1 = this.pointData[i].x - this.pointData[i].width / 2
                let x2 = this.pointData[i].x + this.pointData[i].width / 2
                let y1 = this.pointData[i].y - this.pointData[i].height / 2
                let y2 = this.pointData[i].y + this.pointData[i].height / 2
                minX = Math.min(minX, x1)
                minY = Math.min(minY, y1)
                maxX = Math.max(maxX, x2)
                maxY = Math.max(maxY, y2)
            }
        }
        else{
            for(let i = 0; i < this.pointData.length; i ++){
                let x1 = this.pointData[i].x - this.pointData[i].radius
                let x2 = this.pointData[i].x + this.pointData[i].radius
                let y1 = this.pointData[i].y - this.pointData[i].radius
                let y2 = this.pointData[i].y + this.pointData[i].radius
                minX = Math.min(minX, x1)
                minY = Math.min(minY, y1)
                maxX = Math.max(maxX, x2)
                maxY = Math.max(maxY, y2)
            }
        }


        let cur_width = maxX - minX
        let cur_height = maxY - minY

        // let svgwidth1 = 1200
        // let svgheight1 = 1200

        let svgwidth1 = 1500
        let svgheight1 = 1500

        //let svgwidth2 = 2000
        //let svgheight2 = 2000

        //if(cur_width > svgwidth1){
            scale_ratio_x = svgwidth1 / cur_width
        //}

        //if(cur_height > svgheight1){
            scale_ratio_y = svgheight1 / cur_height
        //}

        let ratio = Math.min(scale_ratio_x, scale_ratio_y)

        transform_dx = 0 - minX
        transform_dy = 0 - minY

        //let ddx = (svgwidth2 / 2)  - ( (minX + transform_dx) * ratio + (maxX + transform_dx) * ratio ) / 2
        //let ddy = (svgheight2 / 2) - ( (minY + transform_dy) * ratio + (maxY + transform_dy) * ratio ) / 2

        for(let i = 0; i < this.pointData.length; i ++){

            this.pointData[i].x = (this.pointData[i].x + transform_dx) * ratio
            this.pointData[i].y = (this.pointData[i].y + transform_dy) * ratio
            this.pointData[i].radius *= ratio
            this.pointData[i].width *= ratio
            this.pointData[i].height *= ratio
            //this.pointData[i].shape = "rectangle"

        }

    }

    handleUpload(){

        let filename = this.selValue

        if(filename == "Manhattan.txt"){
            colorArray = ['#f16b22','#7e519f','#713e3a']
        }
        else if(filename == "simpleScatterplot.json"){
            colorArray = ['#d35d6d','#0885c2','#4aa15f']
        }

        this.setState({
            colorArray:colorArray
        })

        fetch('/data/'+filename,{
            method:'GET',
            headers:{
                'Content-Type':'application/json;charset=UTF-8'
            },
            mode:'cors',
            cache:'default'
        })
            .then(res=>res.json())
            .then(data=>{
                this.setState({
                    hasLoadData:true
                })
                this.pointData = data;
                //for(let i = 0; i < this.pointData.length; i ++){
                //    this.pointData[i].shape = "rectangle"
                //}
                //this.scaleData()

                for (let i=0;i<this.pointData.length;i++){
                    // this.pointData[i].width=this.pointData[i].width*1.5
                    // this.pointData[i].height=this.pointData[i].height*1.5
                    this.pointData[i].radius=10

                }
                //this.setState({
                //    upload: true
                //})

                this.setState({pointData:this.pointData})
                this.shape = data[0].shape;


                this.drawInit();
                this.setState({
                    edges:this.edges
                })
            })

    }

