import React, {Component} from 'react'
import PropTypes from 'prop-types'

class AlgorithmEntry extends Component{

    static propTypes = {
        pointData: PropTypes.array,
    }

    static defaultProps = {
        pointData:[]
    }

    // eslint-disable-next-line
    constructor() {
        super();
    }

    finishInput(){
        if(this.props.pointData){
            alert("Finish input and start creating spanning tree!");
            console.log(this.props.pointData);
        }
    }

    render() {
        return (
            <button onClick = {this.finishInput.bind(this)}>
                Create MST
            </button>
        );
    }
}

export default AlgorithmEntry;