    addCircularEdge(tanPoints){
        function sortDegree(a,b){
            return a.degree-b.degree
        }
        for (let i=0;i<tanPoints.length;i++){
            let points=tanPoints[i]
            for(let e=0;e<this.elementSpace[i].length;e++){
                let eseg=this.elementSpace[i][e]
                if(eseg.type=="arc"){
                    let onpoints=[]
                    let dearray=[]
                    for(let p=0;p<points.length;p++){
                        if(this.isPointOnArc([points[p].x,points[p].y],eseg)){
                            onpoints.push(points[p])
                        }
                    }
                    //sort the points according to degree

                    let startpoint=this.tanGraphPoints[eseg.startid]
                    let endpoint=this.tanGraphPoints[eseg.endid]
                    let startvec=[startpoint.x-eseg.center[0],startpoint.y-eseg.center[1]]

                    for (let p=0;p<onpoints.length;p++){
                        let pointvec=[onpoints[p].x-eseg.center[0],onpoints[p].y-eseg.center[1]]
                        let degree=getDegreeTwoVec(startvec,pointvec)
                        let crossmul=crossMul(startvec,pointvec)
                        if(crossmul>0){
                            degree=2*Math.PI-degree
                        }

                    dearray.push({
                        index:p,
                        degree:degree
                    })
                    }
                    dearray.sort(sortDegree)
                    let thisd=startpoint
                    let nextd
                    for (let d=0;d<dearray.length;d++){
                        nextd=onpoints[dearray[d].index]
                        this.tanGraphEdges.push({
                            type:"arc",
                            startid:thisd.id,
                            endid:nextd.id,
                            center:eseg.center,
                            radius:re
                        })
                        // if(this.tanGraphEdges.length==6001){
                        //     console.log()
                        // }
                        thisd=nextd
                    }
                    this.tanGraphEdges.push({
                        type:"arc",
                        startid:thisd.id,
                        endid:endpoint.id,
                        center:eseg.center,
                        radius:re
                    })

/*                    if(this.tanGraphEdges.length>=60036){
                        console.log()
                    }*/
                }
            }
        }
    }
    findApproxPointOnArc(point,arc){
        let polypoints=arc.polysegment
        let startpoint=arc.start
        let center=arc.center
        let startvec=[startpoint[0]-center[0],startpoint[1]-center[1]]
        let pointvec=[point[0]-center[0],point[1]-center[1]]
        let degree=getDegreeTwoVec(startvec,pointvec)
        let crossmul1=crossMul(startvec,pointvec)
        if(crossmul1>0){
            degree=2*Math.PI-degree
        }
        if(degree<0.000001){
            return 0
        }
        let index=parseInt(degree/deltaDeg)

/*        if(index>polypoints.length-1){
            d3.select("svg").selectAll("circle").remove()
            drawCircle(point[0],point[1],5,"red","none")
            drawCircle(polypoints[polypoints.length-1][0],polypoints[polypoints.length-1][1],5,"blue","none")
            drawCircle(polypoints[0][0],polypoints[0][1],5,"black","none")

        }*/

        return index
    }

    isExist(p,points){
        for(let i=0;i<points.length;i++){
            if(p[0]==points[i].x&&p[1]==points[i].y){
                return points[i].id
            }
        }
        return -1
    }


