    above(P,Vi,Vj)  {
        return this.isLeft(P,Vi,Vj) > 0
    }

    below(P,Vi,Vj)  {
        return this.isLeft(P,Vi,Vj) < 0
    }

    isLeft(  P0,  P1,  P2 )
{
    return (P1.x - P0.x)*(P2.y - P0.y) - (P2.x - P0.x)*(P1.y - P0.y);
}

    RLtangent_PolyPolyC(V,   W) {
        let  ix1, ix2;      // search indices for polygons 1 and 2

        // first get the initial vertex on each polygon
        // ix1 = this.Rtangent_PointPolyC(W[0], V);   // right tangent from W[0] to V
        ix1=this.tangent_PointPoly(W[0],V)[1]
        // ix2 = this.Ltangent_PointPolyC(V[ix1], W); // left tangent from V[ix1] to W

        ix2=this.tangent_PointPoly(V[ix1],W)[0]

        // ping-pong linear search until it stabilizes
        let done = false                    // flag when done
        while (done == false) {
            done = true;                     // assume done until...
            while (this.isLeft(W[(ix2+W.length)%W.length], V[ix1], V[ix1+1]) <= 0){
                ++ix1;                       // get Rtangent from W[ix2] to V
            }

            while (this.isLeft(V[ix1], W[(ix2+W.length)%W.length], W[(ix2-1+W.length)%W.length]) >= 0){
                --ix2;                       // get Ltangent from V[ix1] to W
                done = false;                // not done if had to adjust this
            }
        }
        let t1 = ix1;
        let t2 = ix2;
        if(t1<0){
            t1=t1+V.length
        }
        if(t2<0){
            t2=t2+W.length
        }

        return[t1,t2]
    }



