    drawSplitEdge(){
        for(let i=0;i<this.tanGraphEdges.length;i++){
            if(this.tanGraphEdges[i].type=="arc"){
                let pathstr=""
                let startid=this.tanGraphEdges[i].startid
                let endid=this.tanGraphEdges[i].endid
                let startp=[this.tanGraphPoints[startid].x,this.tanGraphPoints[startid].y]
                let endp=[this.tanGraphPoints[endid].x,this.tanGraphPoints[endid].y]
                let center=this.tanGraphEdges[i].center
                let radius=this.tanGraphEdges[i].radius

                // drawCircle(startp[0],startp[1],2,"red","none")
                // drawCircle(endp[0],endp[1],2,"red","none")

                pathstr+="M "+startp[0]+","+startp[1]
                let largearcflag=0
                let vec1=[startp[0]-center[0],startp[1]-center[1]]

                let vec2=[endp[0]-center[0],endp[1]-center[1]]

                /*                    if(i==5){
                                        drawCircle(center[0],center[1],5,"black","none")

                                        drawCircle(startp[0],startp[1],5,"red","none")
                                        drawCircle(endp[0],endp[1],5,"blue","none")
                                        drawLine(vec1[0],vec1[1],"red")
                                        drawLine(vec2[0],vec2[1],"blue")


                                    }*/

                let crossmul=vec1[0]*vec2[1]-vec2[0]*vec1[1]
                if(crossmul>0){
                    largearcflag=1
                }
                else if(crossmul<=0){
                    largearcflag=0
                }

                pathstr+=" A "+radius+","+radius+" 0 "+largearcflag+" ,0 "+(endp[0])+","+(endp[1])
                d3.select("svg")
                    .append("path")
                    .attr("d",pathstr)
                    .attr("fill","none")
                    .attr("stroke","black")
                    .attr("stroke-width","1")
            }
        }
    }

    calScoreForAll(){
        //cal distance,alpha and crossing num
        console.time("cal score")
        for (let i=0;i<this.tanGraphEdges.length;i++){
            let startid=this.tanGraphEdges[i].startid
            let endid=this.tanGraphEdges[i].endid
            let startpoint=this.tanGraphPoints[startid]
            let endpoint=this.tanGraphPoints[endid]
            let startdataid=startpoint.dataindex
            let enddataid=endpoint.dataindex
            let center=this.tanGraphEdges[i].center
            if(this.tanGraphEdges[i].type=="line"){
                let cat
                if(startpoint.type=="vi"){
                    cat=this.pointData[startpoint.dataindex[0]].cat
                }
                else{
                    cat=this.pointData[startpoint.dataindex].cat
                }
                // console.log(cat)
                let dis=calDistance(startpoint.x,startpoint.y,endpoint.x,endpoint.y)
                this.tanGraphEdges[i].distance=dis
                this.tanGraphEdges[i].crossnum=0
                this.tanGraphEdges[i].alpha=0
                //cal intersection with elespacepath
                let crossnum=0
                for (let e=0;e<this.pathString.length;e++){
                    let cat2=this.pointData[e].cat
                    if(cat!=cat2&&e!=startdataid&&e!=enddataid){
                        const path = ShapeInfo.path(this.pathString[e]);
                        const line = ShapeInfo.line([startpoint.x, startpoint.y], [endpoint.x, endpoint.y]);
                        const intersections = Intersection.intersect(path, line);
                        if(intersections.status=="Intersection"){
                            crossnum+=1
                        }
                    }

                }
                this.tanGraphEdges[i].crossnum=crossnum
            }

            else{
                //arc edges
                let startvec=[startpoint.x-center[0],startpoint.y-center[1]]
                let endvec=[endpoint.x-center[0],endpoint.y-center[1]]
                let degree=getDegreeTwoVec(startvec,endvec)
                let littledegree=degree
                let crossmul1=crossMul(startvec,endvec)
                if(crossmul1>0){
                    degree=2*Math.PI-degree
                }
                let arcLength=degree*re
                this.tanGraphEdges[i].distance=arcLength
                this.tanGraphEdges[i].crossnum=0
/*                if(degree>Math.PI){
                    this.tanGraphEdges[i].alpha=degree-Math.PI
                }
                else{
                    this.tanGraphEdges[i].alpha=Math.PI-degree
                }*/
                this.tanGraphEdges[i].alpha=littledegree

                drawArc([startpoint.x,startpoint.y],[endpoint.x,endpoint.y],center,re)
                // console.log()

            }

            this.tanGraphEdges[i].cl=cd*this.tanGraphEdges[i].distance+calpha*this.tanGraphEdges[i].alpha+ci*this.tanGraphEdges[i].crossnum


        }
        console.timeEnd("cal score")
    }

