    calDistance(node1,node2){
        return Math.sqrt(Math.pow(node1.x-node2.x,2)+Math.pow(node1.y-node2.y,2))
    }

    calEdgeMess(node1,node2){
        let num=0
        for (let i=0;i<this.pointData.length;i++){
            if(this.pointData[i].cat!=node1.cat&&this.pointData[i].cat!=node2.cat){
                if(this.isPointInEdgeRange(node1,node2,this.pointData[i])){
                    let pointedgedis=this.calPointEdgeDistance(node1,node2,this.pointData[i])
                    // console.log(pointedgedis)
                    if(pointedgedis<POINT_EDGE_EPS){
                        num+=1
                    }
                }
            }
        }
        // console.log(num)
        return num
    }

    calEdgeWeight(node1,node2){
        let dis=this.calDistance(node1,node2)
        let mess=this.calEdgeMess(node1,node2)
        return [dis,mess]
    }

    getVecNorm(vec){
        return Math.sqrt(Math.pow(vec[0],2)+Math.pow(vec[1],2))
    }

    calVecCosine(vec1,vec2){
        let dot=vec1[0]*vec2[0]+vec1[1]*vec2[1]
        let norm1=this.getVecNorm(vec1)
        let norm2=this.getVecNorm(vec2)
        return dot/(norm1*norm2)

    }
    isPointInEdgeRange(start,end,point){
        let vec1=[point.x-start.x,point.y-start.y]
        let vec2=[end.x-start.x,end.y-start.y]
        let cos1=this.calVecCosine(vec1,vec2)
        if(cos1<0){
            return false
        }

        let vec3=[point.x-end.x,point.y-end.y]
        let vec4=[start.x-end.x,start.y-end.y]
        let cos2=this.calVecCosine(vec3,vec4)

        if(cos2<0){
            return false
        }

        return true
    }

    calPointEdgeDistance(start,end,point){
        //must check if the point is in the range of the edge before calling this function
        let vec1=[end.x-start.x,end.y-start.y]
        let vec2=[point.x-start.x,point.y-start.y]

        let dot=vec1[0]*vec2[0]+vec1[1]*vec2[1]
        let seg1=dot/this.getVecNorm(vec1)
        let norm=this.getVecNorm(vec2)

        return Math.sqrt(Math.pow(norm,2)-Math.pow(seg1,2))
    }

