import ReactDOM from 'react-dom';
import React, { Component } from 'react'
import * as d3 from "d3"
import "./scale-canvas"
import scaleCanvas from "./scale-canvas";

var colors=['#f700ff','#fbb132','#0885c2','#001aff','#7b28b7','#1c8b3c','#ed334e']

class BMOAlgo extends Component{
    constructor() {
        super();
        this.pointData=[]
        this.itemShape="rectangle"
        this.catPointDict={}
        this.context=null
        this.canvasWidth=1500
        this.canvasHeight=1200
        this.canvasRatio=1
    }

    componentDidMount() {
        fetch('/data/BubbleSetExample1.txt',{
            method:'GET',
            headers:{
                'Content-Type':'application/json;charset=UTF-8'
            },
            mode:'cors',
            cache:'default'
        })
            .then(res=>res.json())
            .then(data=>{
                console.log(data)
                this.pointData=data
                this.itemShape=this.pointData[0].shape
                console.log(this.itemShape)
                for (let i=0;i<this.pointData.length;i++){
                    this.pointData[i].x=parseFloat(this.pointData[i].x)
                    this.pointData[i].y=parseFloat(this.pointData[i].y)

                    this.pointData[i].id=i


                    if(!this.catPointDict.hasOwnProperty(this.pointData[i].cat)){
                        this.catPointDict[this.pointData[i].cat]=[]
                    }
                    this.catPointDict[this.pointData[i].cat].push(this.pointData[i])
                }
                console.log(this.catPointDict)


                this.init()
                this.centralizeData()
                this.drawPointData()
                this.loadEdgeData()
                    .then(res=>res.json())
                    .then(data=>{
                        console.log(data)
                        this.edges=data
                        for (let i=0;i<this.edges.length;i++){
                            this.edges[i].start=parseInt(this.edges[i].start)
                            this.edges[i].end=parseInt(this.edges[i].end)

                        }
                        this.drawEdges()
                        this.getPixelImage()

                    })

            })

    }

    getPixelImage(){
        console.log(this.canvasRatio)

        // this.context.strokeStyle="red"
        // this.context.lineWidth=2
        // this.context.rect(imageX,imageY,imageWidth,imageHeight)
        // this.context.stroke()

        // console.log(imageWidth,imageHeight)
        let imageData=this.context.getImageData(0,0,this.canvasWidth*this.canvasRatio,this.canvasHeight*this.canvasRatio)
        this.context.clearRect(0,0,this.canvasWidth*this.canvasRatio,this.canvasHeight*this.canvasRatio)
        // let data=imageData.data

        let minx=Number.MAX_VALUE
        let miny=Number.MAX_VALUE
        let maxx=Number.MIN_VALUE
        let maxy=Number.MIN_VALUE



        for (let i=0;i<imageData.data.length;i+=4){
            // if(imageData.data[i]>0||imageData.data[i+1]>0||imageData.data[i+2]>0){
                if(imageData.data[i+3]>0){

                    // console.log(imageData.data[i],imageData.data[i+1],imageData.data[i+2])
                // console.log(i)
                // imageData.data[i]=255
                // imageData.data[i+1]=0
                // imageData.data[i+2]=0
                let index=i/4
                let x=index%imageData.width
                let y=parseInt(index/imageData.width)
                // imageData.data[i+3]=0
                if(y<miny){
                    miny=y
                }
            if(y>maxy){
                maxy=y
            }
            if(x<minx){
                minx=x
            }
            if(x>maxx){
                maxx=x
            }
            }
        }
        // this.context.strokeStyle="red"
        // this.context.lineWidth=2
        // this.context.rect(minx,miny,maxx-minx,maxy-miny)
        // this.context.stroke()

        this.context.putImageData(imageData,0,0)

        console.log(imageData)

        let width=maxx-minx
        let height=maxy-miny
        let newImageData=[]
        for(let i=miny;i<maxy+1;i++){
            let oneline=[]

            for (let j=minx;j<maxx+1;j++){
                let index=i*imageData.width+j
                if(imageData.data[index*4+3]>0){
                    oneline.push(1)
                }
                else{
                    oneline.push(0)
                }
            }
            newImageData.push(oneline)
        }
        console.log(newImageData)

        fetch('http://localhost:3001/processImage',{
            method:'post',//post
            mode: 'cors',//
            headers: {//
                'Content-Type': 'application/x-www-form-urlencoded'
            },
            body:'image='+JSON.stringify(newImageData)//
        })
            .then(res=>res.text())
            .then(data=>{
                console.log(data)

            })
        
        // this.context.clearRect(0,0,this.canvasWidth,this.canvasHeight)
        // this.context.clearRect(0,0,this.canvasWidth*this.canvasRatio,this.canvasHeight*this.canvasRatio)

    }

    drawLine(x1,y1,x2,y2,width,color){
        this.context.lineWidth = width;          //
        this.context.strokeStyle = color ;  //
        this.context.moveTo (x1,y1);       //
        this.context.lineTo (x2,y2);       //

        this.context.stroke();
    }

    drawEdges(){
        // d3.selectAll("line").remove()

        for (let i=0;i<this.edges.length;i++){
            let start=this.edges[i].start
            let end=this.edges[i].end
            let index=i
            this.drawLine(this.pointData[start].x,this.pointData[start].y,this.pointData[end].x,this.pointData[end].y,2,"black")
        }
        // console.log(JSON.stringify(this.edges))
    }

    loadEdgeData(){
        let that=this
        return fetch('/data/BubbleSetEdge1.txt',{
            method:'GET',
            headers:{
                'Content-Type':'application/json;charset=UTF-8'
            },
            mode:'cors',
            cache:'default'
        })

    }

    init(){
        this.context=document.getElementById("maincanvas").getContext("2d")
        this.canvasRatio=scaleCanvas(document.getElementById("maincanvas"),this.context,this.canvasWidth,this.canvasHeight)
        // console.log(this.context)
    }

    centralizeData(){
        let minx=Number.MAX_VALUE
        let miny=Number.MAX_VALUE
        let maxx=Number.MIN_VALUE
        let maxy=Number.MIN_VALUE

        for (let i=0;i<this.pointData.length;i++){
            if (this.pointData[i].x>maxx)
                maxx=this.pointData[i].x
            if(this.pointData[i].y>maxy)
                maxy=this.pointData[i].y
            if(this.pointData[i].x<minx)
                minx=this.pointData[i].x
            if(this.pointData[i].y<miny)
                miny=this.pointData[i].y
        }

                let centerx=minx+(maxx-minx)/2
                let centery=miny+(maxy-miny)/2
                let move=[this.canvasWidth/2-centerx,this.canvasHeight/2-centery]

                for (let i=0;i<this.pointData.length;i++){
                    this.pointData[i].x+=move[0]
                    this.pointData[i].y+=move[1]
                }
        this.maxX=maxx+move[0]
        this.minX=minx+move[0]
        this.maxY=maxy+move[1]
        this.minY=miny+move[1]
        // this.drawRect(this.minX,this.minY,this.maxX-this.minX,this.maxY-this.minY,"red")
        console.log(this.minX,this.maxX,this.minY,this.maxY)
        /*        for (let i=0;i<this.pointData.length;i++){
                    this.pointData[i].x-=this.minX-20
                    this.pointData[i].y-=this.minY-20
                }*/
    }

    drawPointData(){
        for(let i=0;i<this.pointData.length;i++){
            this.drawPoint(this.pointData[i].x,this.pointData[i].y,this.pointData[i].radius,colors[this.pointData[i].cat])
        }
    }


    drawPoint(x,y,r,color){

        this.context.beginPath()
        this.context.fillStyle=color
        this.context.arc(x, y, r, 0, Math.PI * 2, false);
        this.context.fill()
        this.context.closePath()
    }

    drawRect(x,y,w,h,color){
        this.context.lineWidth=2
        this.context.strokeStyle=color
        this.context.rect(x-w/2,y-h/2,w,h)
        this.context.stroke()
    }

    deletePoint(e){
        e.stopPropagation()
        let el=e.target
        let index=d3.select(el).attr("index")
        this.points[index]=-1
        d3.select(el).remove()
        console.log(index)
        d3.selectAll("text")
            .filter(function(d,i){
                return d3.select(this).attr("index")==index
            })
            .remove()
        // console.log(this.points)
    }


    render(){
        return(
            <div>
                <canvas id={"maincanvas"} width={"1500"} height={"1200"} >
                </canvas>
                {/*<button onClick={this.addCategory.bind(this)}  style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1550px",bottom:"800px"}}>Add Category</button>*/}
                {/*<input ref={(input)=>this.input=input}  style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1550px",bottom:"750px"}}></input>*/}
                {/*<button onClick={this.switchCategory.bind(this)}  style={{width:200,height:40,fontSize:"20px",position:"absolute",left:"1710px",bottom:"750px",marginLeft:10}}>Change Category</button>*/}
                {/*<button onClick={this.savePoints.bind(this)} style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1550px",bottom:"700px",marginLeft:10}}>Save</button>*/}
                {/*<button onClick={this.loadPoints.bind(this)} style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1710px",bottom:"700px",marginLeft:10}}>Load</button>*/}
                {/*<input ref={(input)=>this.input2=input}  style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1550px",bottom:"650px"}}></input>*/}
                {/*<button onClick={this.changeWidthHeight.bind(this)} style={{width:150,height:40,fontSize:"20px",position:"absolute",left:"1710px",bottom:"650px",marginLeft:10}}>Change Width and Height</button>*/}
                {/*<label ref={(jsonoutput)=>this.jsonoutput=jsonoutput} style={{width:300,height:200,fontSize:"10px",position:"absolute",left:"1550px",bottom:"300px",marginLeft:10,overflow:"scroll"}}>*/}

                {/*</label>*/}
            </div>)
    }
}

export default BMOAlgo