    checkConnect(){
        // this.drawPointData()
        // this.drawEdges()
        for(let k in this.catPointDict){
            let start=this.catPointDict[k][0].id
            var dfs = new jsgraphs.DepthFirstSearch(this.graph, start);
            for (let i=1;i<this.catPointDict[k].length;i++){
                if (!dfs.hasPathTo(this.catPointDict[k][i].id)){
                    return false
                }
            }
        }
        return true
    }

