    drawPointData(){
        let that=this

        function dragged(event) {
            // console.log("end!")
            d3.select(this)
                .attr("cx",event.x)
                .attr("cy",event.y)
        }
        function dragstart() {
            // console.log("start!")
            // d3.select(this).classed("fixed", true);
        }

        var drag = d3.drag()
            .on("start",dragstart)
            .on("drag",dragged)

        for (let i=0;i<this.pointData.length;i++){
            let index=i
            d3.select("svg")
                .append("circle")
                .attr("cx",this.pointData[i].x)
                .attr("cy",this.pointData[i].y)
                .attr("r",this.pointData[i].radius)
                .attr("fill",colors[this.pointData[i].cat])
                .attr("catefory",this.pointData[i].cat)
                .attr("index",index)
                .on("mouseover",function(d,i){
                    console.log(that.pointData[index])
                })
                .on("drag",function(){
                    console.log("drag!")
                })
                .call(drag)
        }
    }

    countEdgeCrossing(){
        let count=0
        let maxOneCross=Number.MIN_VALUE
        for(let i=0;i<this.edges.length;i++){
            let start1=this.pointData[this.edges[i].start]
            let end1=this.pointData[this.edges[i].end]
            let oneCrossNum=0

            for (let j=i+1;j<this.edges.length;j++){
                let start2=this.pointData[this.edges[j].start]
                let end2=this.pointData[this.edges[j].end]
                if(start1.cat!=start2.cat){
                    if(this.checkEdgeCrossing(start1,end1,start2,end2)){
                        count+=1
                        oneCrossNum+=1
                    }
                }
            }

            if(oneCrossNum>maxOneCross){
                maxOneCross=oneCrossNum
            }
        }
        console.log("num of edge crossing: "+count)
        console.log("max crossing num of one edge: "+maxOneCross)

    }

