    sortEdgesInCategory(cat){
        function sortEdgesfunc(a,b){
            return a[2]-b[2]
        }
        let edges=[]
        for (let i=0;i<this.catPointDict[cat].length;i++){
            for (let j=i+1;j<this.catPointDict[cat].length;j++){
                let score=this.calEdgeWeight(this.catPointDict[cat][i],this.catPointDict[cat][j])
                edges.push([this.catPointDict[cat][i].id,this.catPointDict[cat][j].id,score])
            }
        }
        edges.sort(sortEdgesfunc)
        return edges
    }

    checkEdgeCrossing(P1,P2,Q1,Q2){
        if(Math.min(P1.x,P2.x) <= Math.max(Q1.x,Q2.x) &&
            Math.min(Q1.x,Q2.x) <= Math.max(P1.x,P2.x) &&
            Math.min(P1.y,P2.y) <= Math.max(Q1.y,Q2.y) &&
            Math.min(Q1.y,Q2.y) <= Math.max(P1.y,P2.y)){
            if(
                ((Q1.x-P1.x)*(Q1.y-Q2.y)-(Q1.y-P1.y)*( Q1.x-Q2.x)) * ((Q1.x-P2.x)*(Q1.y-Q2.y)-(Q1.y-P2.y)*(Q1.x-Q2.x)) < 0 &&
                ((P1.x-Q1.x)*(P1.y-P2.y)-(P1.y-Q1.y)*(P1.x-P2.x)) * ((P1.x-Q2.x)*(P1.y-P2.y)-(P1.y-Q2.y)*( P1.x-P2.x)) < 0
            ){
                return true;
            }
            else{
                return false;
            }
        }
        else{
            return false
        }
    }

    calEdgeWeightWithCrossing(node1,node2){
        let dis=this.calDistance(node1,node2)
        let mess=this.calEdgeMess(node1,node2)

        let crossnum=0
        for (let i=0;i<this.edges.length;i++){
            let start=this.edges[i].start
            let end=this.edges[i].end
            let edgecat=this.pointData[start].cat
            if(node1.cat!=edgecat&&this.checkEdgeCrossing(node1,node2,this.pointData[start],this.pointData[end])){
                crossnum+=1
            }
        }

        return [dis*(mess+1)*(crossnum+1)/this.maxDistance,dis/this.maxDistance,mess,crossnum]

    }
