//https://github.com/janogonzalez/priorityqueuejs

module.exports = PriorityQueue;

function PriorityQueue(d) {
    this._d = d;
    this._elements = [];
}

PriorityQueue.prototype.checkEdgeCrossing = function(P1,P2,Q1,Q2){
    if(Math.min(P1.x,P2.x) <= Math.max(Q1.x,Q2.x) &&
        Math.min(Q1.x,Q2.x) <= Math.max(P1.x,P2.x) &&
        Math.min(P1.y,P2.y) <= Math.max(Q1.y,Q2.y) &&
        Math.min(Q1.y,Q2.y) <= Math.max(P1.y,P2.y)){
        if(
            ((Q1.x-P1.x)*(Q1.y-Q2.y)-(Q1.y-P1.y)*( Q1.x-Q2.x)) * ((Q1.x-P2.x)*(Q1.y-Q2.y)-(Q1.y-P2.y)*(Q1.x-Q2.x)) < 0 &&
            ((P1.x-Q1.x)*(P1.y-P2.y)-(P1.y-Q1.y)*(P1.x-P2.x)) * ((P1.x-Q2.x)*(P1.y-P2.y)-(P1.y-Q2.y)*( P1.x-P2.x)) < 0
        ){
            return true;
        }
        else{
            return false;
        }
    }
    else{
        return false
    }
}

PriorityQueue.prototype.isEmpty = function() {
    return this.size() === 0;
};

PriorityQueue.prototype.peek = function() {
    if (this.isEmpty()) throw new Error('PriorityQueue is empty');
    return this._elements[0];
};

PriorityQueue.prototype.modify_Prim = function(e1,e2,thisid,weightcross){
    //let a = this._elements.length
    //console.log("modify before: " + a)
    let szbefore = this._elements.length;
    for(let i = this._elements.length - 1;i >= 0;i --){
        let s = this._d[this._elements[i]].start;
        let e = this._d[this._elements[i]].end;
        let id = this._d[this._elements[i]].clusterid2;
        if((!this._d.hasOwnProperty(this._elements[i])) || id == thisid){
            this._elements.splice(i,1);
            continue;
        }
        if(this.checkEdgeCrossing(s,e,e1,e2) && s.cat != e1.cat && e.cat != e2.cat){
            this._d[this._elements[i]].score += weightcross
        }
    }
    let tmp = this._elements;
    this._elements = [];
    for(let i = 0;i< tmp.length;i++){
        this.enq(tmp[i]);
    }

    let sz = this._elements.length;
    if(sz <= 1 || sz == szbefore){
        return;
    }
    for(let i = parseInt((sz - 2) / 2);i >= 0;i --){
        this.adjust(i,sz - 1);
    }

}

PriorityQueue.prototype.searchFather = function (f,x){
    while(x!=f[x]){
        f[x] = f[f[x]];
        x = f[x];
    }
    return x;
}

PriorityQueue.prototype.adjust = function(start, m){
    let i = start;
    let j = 2 * i + 1;
    let tmp = this._elements[i];
    while(j <= m){
        if(j < m && this._compare(j, j + 1)){
            j ++;
        }
        if(this._d[tmp].score <= this._d[this._elements[j]].score){
            break;
        }
        else{
            this._elements[i] = this._elements[j];
            //V1
            //this._d[this._elements[i]].pid = i;
            i = j;
            j = 2 * j + 1;
        }
    }
    this._elements[i] = tmp;
    //V1
    //this._d[this._elements[i]].pid = i;
}
