PriorityQueue.prototype.modify_Kruskal_V2 = function(e1,e2,f,weightcross){

    //let szbefore = this._elements.length;
    let tmp = []
    for(let i = this._elements.length - 1;i >= 0;i --){
        let s = this._d[this._elements[i]].start;
        let e = this._d[this._elements[i]].end;
        let fs = this.searchFather(f,this._d[this._elements[i]].clusterid1);
        let fe = this.searchFather(f,this._d[this._elements[i]].clusterid2);
        if((!this._d.hasOwnProperty(this._elements[i]))){
            this._elements.splice(i,1);
            continue;
        }
        if(fs == fe){
            delete this._d[this._elements[i]];
            this._elements.splice(i,1);
            continue;
        }
        if(this.checkEdgeCrossing(s,e,e1,e2) && s.cat != e1.cat && e.cat != e2.cat){
            this._d[this._elements[i]].score += weightcross
        }
        tmp.push(this._elements[i])
    }

    let sz = tmp.length;

    if(sz <= 1){
        return;
    }

    this._elements=[]

    for(let i = 0;i < sz;i ++){
        this.enq(tmp[i]);
    }

    //for(let i = parseInt((sz - 2) / 2);i >= 0;i --){
    //    this.adjust(i,sz - 1);
    //}

}

