PriorityQueue.prototype.deq = function() {
    //let a = this._elements.length
    //console.log("deq before: " + a)
    //delete this._d[this._elements[0]];
    var first = this.peek();
    //V1
    //this._d[first].pid = -1;
    var last = this._elements.pop();
    var size = this.size();
    if (size === 0) return first;

    this._elements[0] = last;
    var current = 0;

    while (current < size) {
        var largest = current;
        var left = (2 * current) + 1;
        var right = (2 * current) + 2;

        if (left < size && this._compare(left, largest) >= 0) {
            largest = left;
        }

        if (right < size && this._compare(right, largest) >= 0) {
            largest = right;
        }

        if (largest === current) break;

        this._swap(largest, current);
        current = largest;
    }
    //let b = this._elements.length
    //console.log("deq after: " + b)
    return first;
};

PriorityQueue.prototype.enq = function(element) {
    //let a = this._elements.length
    //console.log("enq before: " + a)
    var size = this._elements.push(element);
    var current = size - 1;

    while (current > 0) {
        var parent = Math.floor((current - 1) / 2);
        if (this._compare(current, parent) <= 0) break;
        this._swap(parent, current);
        current = parent;
    }
   //let b = this._elements.length
   //console.log("enq after: " + b)
    return size;
};

PriorityQueue.prototype.size = function() {
    return this._elements.length;
};

PriorityQueue.prototype.forEach = function(fn) {
    return this._elements.forEach(fn);
};

PriorityQueue.prototype._compare = function(a, b) {
    return this._d[this._elements[b]].score - this._d[this._elements[a]].score;
};

PriorityQueue.prototype._swap = function(a, b) {
    var aux = this._elements[a];
    this._d[this._elements[a]].pid = b;
    this._d[this._elements[b]].pid = a;
    this._elements[a] = this._elements[b];
    this._elements[b] = aux;
};