import ReactDOM from 'react-dom';
import React, { Component } from 'react'

import * as d3 from "d3"

var colors=['#f700ff','#fbb132','#0885c2','#001aff','#7b28b7','#1c8b3c','#ed334e']

class GapMinderNew extends  Component{
    constructor() {
        super();
        this.population=[]
        this.fertility=[]
        this.lifeExp=[]
        this.data=[]
        this.pointData=[]
    }

    componentDidMount() {
        let that=this
        d3.csv("data/GapMinder-new/population.csv",function(data){
            // console.log(data)
                // console.log(data[i])
                let country=data.Country
                that.data.push({
                    country:country,
                    population:parseInt(data.Pop1985),
                    fertility:parseFloat(data.F1985),
                    lifeExp:parseFloat(data.L1985),
                    geo:data.Group
                })
        }).then(function(data){
            let catDict={}
            let catnum=0
            for (let i=0;i<that.data.length;i++){
                if(!catDict.hasOwnProperty(that.data[i].geo)){
                    catDict[that.data[i].geo]=catnum
                    catnum++
                }
            }
            for (let i=0;i<that.data.length;i++){
                that.data[i].cat=catDict[that.data[i].geo]
            }
            console.log(that.data)
            that.drawScatterPlot()

        })

    }

    drawScatterPlot(){


        let xscale=d3.scaleLinear().domain([d3.min(this.data.map((d)=>{return parseFloat(d.fertility)})),d3.max(this.data.map((d)=>{return d.fertility}))])
            .range([50,900])
        let yscale=d3.scaleLinear().domain([d3.min(this.data.map((d)=>{return parseFloat(d.lifeExp)})),d3.max(this.data.map((d)=>{return d.lifeExp}))])
            .range([900,0])
        let rscale=d3.scaleSqrt().domain([d3.min(this.data.map((d)=>{return d.population})),d3.max(this.data.map((d)=>{return d.population}))])
            .range([5,20])

        for (let i=0;i<this.data.length;i++){
            this.data[i].id=i
            this.drawPoint(xscale(this.data[i].fertility),yscale(this.data[i].lifeExp),rscale(this.data[i].population),colors[this.data[i].cat],this.data[i].cat,i)
        }


        this.savePointData()
    }

    drawPoint(x,y,r,color,cat,index){

        function dragged(event) {
            // console.log("end!")
            d3.select(this)
                .attr("cx",event.x)
                .attr("cy",event.y)
        }
        function dragstart() {
            // console.log("start!")
            // d3.select(this).classed("fixed", true);
        }

        var drag = d3.drag()
            .on("start",dragstart)
            .on("drag",dragged)

        let that=this
        d3.select("svg")
            .append("circle")
            .attr("cx",x)
            .attr("cy",y)
            .attr("r",r)
            .attr("fill",color)
            .attr("catefory",cat)
            .attr("index",index)
            .attr("stroke","black")
            .on("mouseover",function(d,i){
                console.log(that.data[index])
            })
            .call(drag)
        this.pointData.push({
            x:x,
            y:y,
            cat:cat,
            id:index,
            shape:"circle",
            radius:r,
            width:r,
            height:r
        })
    }

    savePointData(){
        let that=this
        d3.selectAll("circle")
            .each(function(){
                let x=d3.select(this).attr("cx")
                let y=d3.select(this).attr("cy")
                let index=d3.select(this).attr("index")
                that.pointData[index].x=parseFloat(x)
                that.pointData[index].y=parseFloat(y)
            })
        console.log(that.pointData)
        console.log(JSON.stringify(that.pointData))
    }

    render() {
        return(<div id={"svgcontainer"}>
            <svg id={"mainsvg"} width={"1000px"} height={"1000px"}>
            </svg>
            <button onClick={this.savePointData.bind(this)}> Save </button>
        </div>)
    }
}

export default GapMinderNew