    preProcessPoints(points, pdict){
        for(let i = 0;i < points.length; i ++){
            let tmp
            if(points[i].flag == true){
                tmp = this.pointData[points[i].id]
                tmp.flag = true
            }
            else{
                tmp = points[i]
            }
            for (let c = 0;c < tmp.catArray.length; c ++){
                let nowcat = tmp.catArray[c]
                if(!pdict.hasOwnProperty(nowcat)){
                    pdict[nowcat] = []
                }
                pdict[nowcat].push(tmp)
            }
        }
    }

    preProcessEdges(edges, edict, filledges){
        //console.log("preProcessEdges")
        //console.log(edges)
        //console.log(this.edges)
        for(let i = 0;i < edges.length; i ++){
            let tmp
            let catarr
            if(edges[i].flag == true){
                tmp = this.edges[edges[i].id]
                tmp.flag = true
                catarr = this.pointData[tmp.start].catArray
                filledges.push(tmp)
            }
            else{
                tmp = edges[i]
                catarr = tmp.startpoint.catArray
            }
            for (let c = 0;c < catarr.length; c ++){
                let nowcat = catarr[c]
                if(!edict.hasOwnProperty(nowcat)){
                    edict[nowcat] = []
                }
                edict[nowcat].push(tmp)
            }
        }
    }

