    calEdgeLength(){
        let minlength=Number.MAX_VALUE
        let maxlength=Number.MIN_VALUE
        for(let i=0;i<this.edges.length;i++){
            let startpoint=this.pointData[this.edges[i].start]
            let endpoint=this.pointData[this.edges[i].end]
            if(this.edges[i].virtrual){
                let vpoint=this.edges[i].virtrualNodes
                let nowp
                let nextp
                let edgelength=0
                nowp=[startpoint.x,startpoint.y]
                for (let i=0;i<vpoint.length;i++){
                    nextp=vpoint[i]
                    edgelength+=calDistance(nowp[0],nowp[1],nextp[0],nextp[1])
                    nowp=nextp
                }
                edgelength+=calDistance(nowp[0],nowp[1],endpoint.x,endpoint.y)

                if(edgelength>maxlength){
                    maxlength=edgelength
                }
                if(edgelength<minlength){
                    minlength=edgelength
                }

            }
            else{

                let edgelength=calDistance(startpoint.x,startpoint.y,endpoint.x,endpoint.y)
                if(edgelength>maxlength){
                    maxlength=edgelength
                }
                if(edgelength<minlength){
                    minlength=edgelength
                }

            }

        }
        this.maxEdgeLength=maxlength
        this.minEdgeLength=minlength

    }

