    calActiveRegion(flag){

        for (var k in this.catPointDict){

            if(flag || !this.catEnergyField.hasOwnProperty(k)){
                this.catEnergyField[k]={}
                //console.log("calActiveRegion:")
                //console.log(k)
            }

            let maxx=this.catPointDict[k].maxX
            let minx=this.catPointDict[k].minX
            let maxy=this.catPointDict[k].maxY
            let miny=this.catPointDict[k].minY

            //console.log("active region: ")
            //console.log([maxx, minx, maxy, miny])

            this.catEnergyField[k].boundingX=minx-Math.max(nodeR1,edgeR1)-buffer
            this.catEnergyField[k].boundingY=miny-Math.max(nodeR1,edgeR1)-buffer

            this.catEnergyField[k].boundingWidth=Math.ceil((maxx-minx+Math.max(nodeR1,edgeR1)*2+buffer*2)/pixelGroup)
            this.catEnergyField[k].boundingHeight=Math.ceil((maxy-miny+Math.max(nodeR1,edgeR1)*2+buffer*2)/pixelGroup)

            if(flag || !this.catEnergyField[k].hasOwnProperty("field")){

                let centerX = this.catEnergyField[k].boundingX + 0.5 * this.catEnergyField[k].boundingWidth
                let centerY = this.catEnergyField[k].boundingY + 0.5 * this.catEnergyField[k].boundingHeight

                this.catEnergyField[k].x = parseInt((centerX - 0.5 * svgwidth))
                this.catEnergyField[k].y = parseInt((centerY - 0.5 * svgheight))

                this.catEnergyField[k].width = parseInt(svgwidth / pixelGroup)
                this.catEnergyField[k].height = parseInt(svgheight / pixelGroup)

                this.catEnergyField[k].field=[]
                this.catEnergyField[k].barrierMap=[]
                for (let i=0;i<svgheight;i++){
                    this.catEnergyField[k].field.push(new Array(svgwidth).fill(0))
                    this.catEnergyField[k].barrierMap.push(new Array(svgwidth).fill(0))
                }

            }

        }

        this.hasBubbles = true

    }

