    getPointEdgeDistance(lx1, ly1, lx2, ly2, x, y) {
        var x1 = lx1;
        var y1 = ly1;
        var x2 = lx2 - x1;
        var y2 = ly2 - y1;
        var px = x - x1;
        var py = y - y1;
        var dotprod = px * x2 + py * y2;
        var projlenSq;
        if(dotprod <= 0) {
            projlenSq = 0;
        } else {
            px = x2 - px;
            py = y2 - py;
            dotprod = px * x2 + py * y2;
            if(dotprod <= 0) {
                projlenSq = 0;
            } else {
                projlenSq = dotprod * dotprod / (x2 * x2 + y2 * y2);
            }
        }
        var lenSq = px * px + py * py - projlenSq;
        if(lenSq < 0) {
            lenSq = 0;
        }
        return Math.sqrt(lenSq);
    }

    calDistanceToEdgeV2(x,y,edge){
        //return the distance to the edge and distance to two nodes
        let startpoint, endpoint
        if(edge.flag == false){
            startpoint = edge.startpoint
            endpoint = edge.endpoint
        }
        else{
            startpoint=this.pointData[edge.start]
            endpoint=this.pointData[edge.end]
        }
        if(edge.virtrual && edge.virtrualNodes.length > 0){
            let vpoint=edge.virtrualNodes
            let nowp=[startpoint.x,startpoint.y]
            let nextp
            let mindis=Number.MAX_VALUE
            let distancetostartnode=0
            let distancetoendnode=0
            let distancetoedge=0
            let totaldistance=0
            let minindex=-1
            for (let i=0;i<vpoint.length;i++){
                nextp=vpoint[i]
                let dis=this.getPointEdgeDistance(nowp[0],nowp[1],nextp[0],nextp[1],x,y)
                if(dis<mindis){
                    mindis=dis
                    minindex=i
                }
                nowp=nextp
            }

            nowp=[startpoint.x,startpoint.y]
            for(let i=0;i<minindex;i++){
                nextp=vpoint[i]
                distancetostartnode+=calDistance(nowp[0],nowp[1],nextp[0],nextp[1])
                nowp=nextp
            }
            if(minindex==0){
                nowp=[startpoint.x,startpoint.y]
                nextp=vpoint[0]
            }
            else{
                nowp=vpoint[minindex-1]
                nextp=vpoint[minindex]
            }

            let enddis=this.getPointEdgeDistance(endpoint.x,endpoint.y,vpoint[vpoint.length-1][0],vpoint[vpoint.length-1][1],x,y)
            if(enddis<mindis){
                mindis=enddis
                distancetostartnode+=calDistance(nowp[0],nowp[1],nextp[0],nextp[1])
                nowp=nextp
                nextp=[endpoint.x,endpoint.y]
            }

            distancetoedge=mindis

            let sp=[x-nowp[0],y-nowp[1]]
            let se=[nextp[0]-nowp[0],nextp[1]-nowp[1]]
            let es=[nowp[0]-nextp[0],nowp[1]-nextp[1]]
            let ep=[x-nextp[0],x-nextp[1]]
            let selength=calDistance(nowp[0],nowp[1],nextp[0],nextp[1])
            let sesp=se[0]*sp[0]+se[1]*sp[1]
            let esep=es[0]*ep[0]+es[1*ep[1]]
            let n=sesp/selength

            // if(sesp>0&&esep>0){
            distancetostartnode+=n
            // }


            nowp=[startpoint.x,startpoint.y]
            for(let i=0;i<vpoint.length;i++){
                nextp=vpoint[i]
                totaldistance+=calDistance(nowp[0],nowp[1],nextp[0],nextp[1])
                nowp=nextp
            }
            totaldistance+=calDistance(nowp[0],nowp[1],endpoint.x,endpoint.y)
            distancetoendnode=totaldistance-distancetostartnode
            let distancetonode=Math.min(distancetostartnode,distancetoendnode)

            if(distancetonode<0){
                distancetonode=-1
                // drawRect(x,y,5,5,"red")
                // console.log()
            }

            return [distancetoedge,distancetonode,totaldistance]

        }
        else{
            let distancetoedge=this.getPointEdgeDistance(startpoint.x,startpoint.y,endpoint.x,endpoint.y,x,y)
            let sp=[x-startpoint.x,y-startpoint.y]
            let se=[endpoint.x-startpoint.x,endpoint.y-startpoint.y]
            let es=[startpoint.x-endpoint.x,startpoint.y-endpoint.y]
            let ep=[x-endpoint.x,y-endpoint.y]

            let selength=calDistance(startpoint.x,startpoint.y,endpoint.x,endpoint.y)
            let sesp=se[0]*sp[0]+se[1]*sp[1]
            let esep=es[0]*ep[0]+es[1]*ep[1]


            let n=sesp/selength

            // let dir=[se[0]/selength*n,se[1]/selength*n]
            // let newp=[se[0]+dir[0],se[1]+dir[1]]


            let distancetostartnode=n
            let distancetoendnode=selength-n
            let distancetonode=Math.min(distancetostartnode,distancetoendnode)
            if(distancetonode<0){
                distancetonode=-1

            }

            let totaldistance=selength

            return [distancetoedge,distancetonode,totaldistance]
        }


    }

