    isRectIntersects (rect1,rect2) {
        if(rect1.width <= 0 || rect1.height <= 0 || rect2.width <= 0 || rect2.height <= 0) return false;
        return (rect2.maxX > rect1.minX &&
            rect2.maxY > rect1.minY &&
            rect2.minX < rect1.maxX &&
            rect2.minY < rect1.maxY);
    };

