                                    if(distance<nodeR1+10){
                                        let realx=x * pixelGroup + this.catEnergyField[k].x
                                        let realy=y * pixelGroup + this.catEnergyField[k].y

                                        let relativex=Math.floor((realx-this.catEnergyField[catarr[c]].x)/pixelGroup)
                                        let relativey=Math.floor((realy-this.catEnergyField[catarr[c]].y)/pixelGroup)
                                        let energy=nodeWeight*Math.pow(nodeR1-distance,2)/Math.pow(nodeR1-nodeR0,2)
                                        if(relativex>0&&relativey>0&&relativex<this.catEnergyField[catarr[c]].width&&relativey<this.catEnergyField[catarr[c]].height){
                                            // this.catEnergyField[catarr[c]].field[relativey][relativex]+=energy
                                        }
                                    }
