    BSplineInterpolate(points){
        let tpoints=[]
        let splinePoints=[]
        let gap=5
        for (let i=0;i<points.length;i++){
            let remainLength=points.length-i

            if(remainLength>gap){
                if(i%gap==0){
                    tpoints.push(points[i])
                }
                if(i>0&&i%(3*gap)==0){
                    var spline = new BSpline(tpoints,3)
                    for(var t = 0.01;t<=1;t+=0.01){
                        var p = spline.calcAt(t);
                        splinePoints.push(p)
                    }
                    tpoints=[]
                    tpoints.push(points[i])
                }
            }
            else{
                if(i%3==0){
                    tpoints.push(points[i])
                }
            }


        }
        var spline = new BSpline(tpoints,3)
        for(var t = 0.01;t<=1;t+=0.01){
            var p = spline.calcAt(t);
            splinePoints.push(p)
        }


        return splinePoints
    }

    normalizeKernel(kernel){
        let sum=0
        for(let i=0;i<kernel.length;i++){
            for(let j=0;j<kernel.length;j++){
                sum+=kernel[i][j]
            }
        }

        for(let i=0;i<kernel.length;i++){
            for(let j=0;j<kernel.length;j++){
                kernel[i][j]=kernel[i][j]/sum
            }
        }
        return kernel
    }

    getGaussianKernel(theta,kernelsize){
        let scale=1
        let kernel=[]
        for (let i=0;i<kernelsize;i++){
            kernel.push([])
            for (let j=0;j<kernelsize;j++){
                kernel[i].push(0)
            }
        }
        let anchor=parseInt(kernelsize/2)/scale
        let C=1/(Math.PI*2*theta*theta)

        for (let i=0;i<kernelsize;i++){
            for (let j=0;j<kernelsize;j++){
                let ny=i/scale
                let nx=j/scale
                let y=Math.pow(ny-anchor,2)
                let x=Math.pow(nx-anchor,2)
                kernel[i][j]=C*Math.exp(-(x*x+y*y)/(2*theta*theta))
            }
        }

        kernel=this.normalizeKernel(kernel)
        return kernel
    }

    checkSingularPoint(k,x,y){
        let delta=1
        let field=this.catEnergyField[k].finalField
        let isSingular=true
        //right
        if(x+delta<this.catEnergyField[k].width){
            if(field[y][x+delta]>0){
                isSingular=false
            }
        }
        //left
        if(x-delta>0){
            if(field[y][x-delta]>0){
                isSingular=false
            }
        }
        //up
        if(y-delta>0){
            if(field[y-delta][x]>0){
                isSingular=false
            }
        }
        //down
        if(y+delta<this.catEnergyField[k].height){
            if(field[y+delta][x]>0){
                isSingular=false
            }
        }
        //right-top
        if(x+delta<this.catEnergyField[k].width&&y-delta>0){
            if(field[y-delta][x+delta]>0){
                isSingular=false
            }
        }
        //right-bottom
        if(x+delta<this.catEnergyField[k].width&&y+delta<this.catEnergyField[k].height){
            if(field[y+delta][x+delta]>0){
                isSingular=false
            }
        }
        //left-top
        if(x-delta>0&&y-delta>0){
            if(field[y-delta][x-delta]>0){
                isSingular=false
            }
        }
        //left-bottom
        if(x-delta>0&&y+delta<this.catEnergyField[k].height){
            if(field[y+delta][x-delta]>0){
                isSingular=false
            }
        }

        return isSingular
    }

