    smoothEnergyField(bounding){
        let kernelsize = 3
        let kernel = this.getGaussianKernel(1.5, kernelsize)
        let anchor = parseInt(kernelsize/2)
        // console.log(kernel)
        for (var k in this.catEnergyField){

            let sx = bounding[k].minX;
            let ex = bounding[k].maxX;
            let sy = bounding[k].minY;
            let ey = bounding[k].maxY;

            let cur_width = ex - sx + 1;
            let cur_height = ey - sy + 1;

            let newField=[]
            for (let y = 0;y < cur_height + 10;y ++){
                newField.push(new Array(cur_width + 10).fill(0))
            }

            for (let y = sy; y <= ey; y ++){
                for (let x = sx; x <= ex; x ++){

                    let newvalue = 0
                    for (let i = 0;i < kernelsize;i ++){
                        for (let j = 0;j < kernelsize;j ++){
                            let dy = i - anchor
                            let dx = j - anchor
                            let ny = y + dy
                            let nx = x + dx
                            if(ny > 0 && ny < this.catEnergyField[k].height && nx > 0 && nx < this.catEnergyField[k].width){
                                newvalue += kernel[i][j] * this.catEnergyField[k].finalField[ny][nx]
                            }
                        }
                    }
                    if(newvalue > 0){
                        newField[y - sy][x - sx] = newvalue
                    }

                }
            }

            for(let y = sy; y <= ey; y ++){
                for(let x = sx; x <= ex; x ++){
                    //console.log("(" + y + ", " + x + ")")
                    //console.log(this.catEnergyField[k].finalField)
                    this.catEnergyField[k].finalField[y][x] = newField[y - sy][x - sx]
                }
            }

            //this.catEnergyField[k].finalField=newField
        }
    }

    saveOutline(isoLineDict){
        let res=[]
        for (var k in isoLineDict){
            for (let i=0;i<isoLineDict[k].length;i++){
                let formatpoints=[]
                for(let p=0;p<isoLineDict[k][i].length;p++){
                    formatpoints.push({x:isoLineDict[k][i][p][0],y:isoLineDict[k][i][p][1]})
                }
                res.push({
                    cat:k,
                    id:i,
                    isInterior:false,
                    parentID:-1,
                    points:formatpoints
                })
            }
        }
        console.log(JSON.stringify(res))
    }

