import ReactDOM from 'react-dom';
import React, { Component } from 'react'

import * as d3 from "d3"

var colors=['#ffcc99','#001aff','#c0c0c0','#7b28b7','#1c8b3c','#ed334e']

class Example1 extends  Component{
    constructor() {
        super();
        this.data=[]
        this.pointData=[]
    }

    componentDidMount() {
        let that=this
        d3.csv("data/GapMinder-new/ex1.csv",function(data){
            // console.log(data)
            // console.log(data[i])
            let country=data.Country
            that.data.push({
                x:parseInt(data.xfield),
                y:parseFloat(data.yfield),
                cat:data.cat
            })
        }).then(function(data){
            console.log(that.data)
            that.drawScatterPlot()

        })

    }

    drawScatterPlot(){

        for (let i=0;i<this.data.length;i++){
            this.data[i].id=i
            this.drawPoint(this.data[i].x,this.data[i].y,10,colors[this.data[i].cat],this.data[i].cat,i)
        }


        this.savePointData()
    }

    drawPoint(x,y,r,color,cat,index){

        function dragged(event) {
            // console.log("end!")
            d3.select(this)
                .attr("cx",event.x)
                .attr("cy",event.y)
        }
        function dragstart() {
            // console.log("start!")
            // d3.select(this).classed("fixed", true);
        }

        var drag = d3.drag()
            .on("start",dragstart)
            .on("drag",dragged)

        let that=this
        d3.select("svg")
            .append("rect")
            .attr("x",x-r)
            .attr("y",y-r)
            .attr("width",r)
            .attr("height",r)
            .attr("fill",color)
            .attr("catefory",cat)
            .attr("index",index)
            .attr("stroke","black")
            .on("mouseover",function(d,i){
                console.log(that.data[index])
            })
            .call(drag)
        this.pointData.push({
            x:x,
            y:y,
            cat:cat,
            id:index,
            shape:"rectangle",
            radius:r,
            width:r,
            height:r
        })
    }

    savePointData(){
        let that=this
        d3.selectAll("circle")
            .each(function(){
                let x=d3.select(this).attr("cx")
                let y=d3.select(this).attr("cy")
                let index=d3.select(this).attr("index")
                that.pointData[index].x=parseFloat(x)
                that.pointData[index].y=parseFloat(y)
            })
        console.log(that.pointData)
        console.log(JSON.stringify(that.pointData))
    }

    render() {
        return(<div id={"svgcontainer"}>
            <svg id={"mainsvg"} width={"1000px"} height={"1000px"}>
            </svg>
            <button onClick={this.savePointData.bind(this)}> Save </button>
        </div>)
    }
}

export default Example1