import ReactDOM from 'react-dom';
import React, { Component } from 'react'
import PropTypes from 'prop-types'
import * as d3 from "d3"
import AdaptiveIsovalue from "./AdaptiveIsovalue";
import {Button, Switch} from 'antd';
import {calDistance, PointToSegDist} from "./util"
import * as A from '@svgdotjs/svg.js'
import * as B from '@svgdotjs/svg.topoly.js'
import BSpline from "./BSpline";
const { pathDataToPolys } = require('svg-path-to-polygons');


var IsoInsideTest = require('point-in-polygon');
var colors=['#e1642c','#d35d6d','#cecf7b','#4aa15f','#0885c2','#e2a2be','#ed334e','#6950a1','#1d953f','#ea66a6','#bed742','#2b4490']   //fig2
// var colors=['#fbf4a4','#c4da90','#a7bfd9','#f3c4d3','#f5c3a0','#a5c3af','#b9b9b4','#d2c1d5','#e7f1f5','#f9dce2','#e7f1f5','#cdcadb'] //fig3

class CalOverLap extends Component{

    constructor() {
        super();
    }

    componentDidMount() {

        //this.getPath()
        this.getSVG()

    }


    getSVG(){

        let filename="Citywide.svg"

        fetch('/data/Evaluation/bubbleset/'+filename,{
            method:'GET',
            headers:{
                'Content-Type':'image/svg+xml'
            },
            mode:'cors',
            cache:'default'
        })
            .then(res=>res.text())
            .then(data=>{
                //console.log(data)
                console.log(data)

                let doc = new DOMParser();
                let xml = doc.parseFromString(data,"image/svg+xml")
                let gs = xml.getElementsByTagName("g")
                console.log("select g res:")
                console.log(gs)
                var paths = []
                for(let i = 0; i < gs.length; i ++){
                    let stylestr = gs[i].attributes[0].nodeValue.toString()
                    let cmpstr = "fill: none"
                    if(stylestr.match(cmpstr)==null){
                      paths.push(gs[i].getElementsByTagName("path"))
                    }
                }
                //var paths = xml.getElementsByTagName("path");

                console.log(paths)
                //console.log(xml)
                //console.log(paths)

                //console.log(paths)

                let cat_Polygon = []
                let cat_Area = []
                let cat_Overlap_Area = []

                let minX = Number.MAX_VALUE
                let minY = Number.MAX_VALUE
                let maxX = Number.MIN_VALUE
                let maxY = Number.MIN_VALUE

                for(let i = 0; i < paths.length; i ++){
                    //console.log(paths[i])
                    console.time("to polygon")
                    let path_data = paths[i][0].attributes.d.nodeValue
                    let canvas = A.SVG().addTo('body')
                    let p = canvas.path(path_data)
                    let points = p.toPoly('5px')._array
                    cat_Polygon.push(points)
                    cat_Area.push(0)
                    cat_Overlap_Area.push(0)
                    for(let j = 0; j < points.length; j ++){
                        let x = points[j][0]
                        let y = points[j][1]
                        minX = Math.min(minX, x)
                        minY = Math.min(minY, y)
                        maxX = Math.max(maxX, x)
                        maxY = Math.max(maxY, y)
                    }
                    //console.log(points)
                    d3.select("#mainsvg").append("path")
                        .attr("d",path_data)
                        .attr("fill",colors[i])
                        .style('opacity', 0.5)
                    //for(let j = 0; j < points.length; j ++){
                    //    let x = points[j][0]
                    //    let y = points[j][1]
                    //    d3.select("#mainsvg").append('circle')
                    //        .attr("cx",x)
                    //        .attr("cy",y)
                    //        .attr("r",2)
                    //        .attr("fill","red")
                    //}
                    console.timeEnd("to polygon")
                }

                d3.select("#mainsvg")
                    .attr("viewBox",minX+" "+minY+" "+(maxX-minX)+" "+(maxY-minY))


                minX -= 5
                minY -= 5
                maxX += 5
                maxY += 5

                for(let x = minX; x <= maxX; x ++){
                    for(let y= minY; y <= maxY; y ++){
                        let overlapID = []
                        for(let i = 0; i < cat_Polygon.length; i ++){
                            if(IsoInsideTest([x, y], cat_Polygon[i])){
                                overlapID.push(i)
                                cat_Area[i] ++
                            }
                        }
                        if(overlapID.length > 1){
                            d3.select('#mainsvg').append("circle")
                                .attr("cx",x)
                                .attr("cy",y)
                                .attr("r",1)
                                .attr("fill","red")
                            for(let i = 0; i < overlapID.length; i ++){
                                cat_Overlap_Area[i] ++
                            }
                        }
                    }
                }

                let Sum_Area = 0
                let Overlap_Area = 0

                for(let i = 0; i < cat_Area.length; i ++){
                    Sum_Area += cat_Area[i]
                    Overlap_Area += cat_Overlap_Area[i]
                }

                console.log(filename)
                console.log("OverLap Area: " + Overlap_Area)
                console.log("Sum Area: " + Sum_Area)
                console.log("Result: " + Overlap_Area / Sum_Area)

            })

    }

    getPath(){

        //for(let f = 1; f <= 10; f ++){
            //let filename = "ex" + f.toString() + ".txt"
            //console.log(filename)
            let filename = "Citywide.txt"
            fetch('/data/Evaluation/'+filename,{
                method:'GET',
                headers:{
                    'Content-Type':'application/json;charset=UTF-8'
                },
                mode:'cors',
                cache:'default'
            })
                .then(res=>res.json())
                .then(data=>{
                    //console.log(data)
                    var paths = data

                    let cat_Polygon = {}
                    let cat_Area = {}
                    let cat_Overlap_Area = {}
                    let selectIdx = {}

                    let minX = Number.MAX_VALUE
                    let minY = Number.MAX_VALUE
                    let maxX = Number.MIN_VALUE
                    let maxY = Number.MIN_VALUE

                    //console.log(paths)
                    console.time("cal")
                    for(let k in paths){

                        let draw_data = ""

                        cat_Polygon[k] = []
                        cat_Area[k] = 0
                        cat_Overlap_Area[k] = 0

                        let sID = 0
                        let maxPath = -1

                        console.time("to polygon")
                        for(let i = 0; i < paths[k].length; i ++){
                            let path_data = paths[k][i]
                            draw_data += path_data
                            let canvas = A.SVG().addTo('body')
                            let p = canvas.path(path_data)
                            let points = p.toPoly('5px')._array
                            cat_Polygon[k].push(points)
                            if(points.length > maxPath){
                                maxPath = points.length
                                sID = i
                            }
                            for(let j = 0; j < points.length; j ++){
                                let x = points[j][0]
                                let y = points[j][1]
                                minX = Math.min(minX, x)
                                minY = Math.min(minY, y)
                                maxX = Math.max(maxX, x)
                                maxY = Math.max(maxY, y)
                            }
                        }
                        console.timeEnd("to polygon")

                        selectIdx[k] = sID

                        d3.select("#mainsvg").append("path")
                            .attr("d",draw_data)
                            .style("fill",colors[parseInt(k)])
                            .style('opacity', 0.5)

                    }

                    d3.select("#mainsvg")
                        .attr("viewBox",minX+" "+minY+" "+(maxX-minX)+" "+(maxY-minY))

                    minX -= 5
                    minY -= 5
                    maxX += 5
                    maxY += 5

                    //console.log(cat_Polygon)

                    for(let x = minX; x <= maxX; x ++){
                        for(let y= minY; y <= maxY; y ++){

                            let overlapID = []

                            for(let k in cat_Polygon){

                                let sID = selectIdx[k]

                                if(!IsoInsideTest([x, y], cat_Polygon[k][sID])){
                                    continue
                                }

                                let flag2 = true
                                for(let i = 0; i < cat_Polygon[k].length; i ++){
                                    if(i == sID){
                                        continue
                                    }
                                    if(IsoInsideTest([x, y], cat_Polygon[k][i])){
                                        flag2 = false
                                        break
                                    }
                                }

                                if(flag2){
                                    cat_Area[k] ++
                                    overlapID.push(k)
                                }

                            }

                            if(overlapID.length > 1){
                                d3.select('#mainsvg').append("circle")
                                    .attr("cx",x)
                                    .attr("cy",y)
                                    .attr("r",1)
                                    .attr("fill","red")
                                for(let i = 0; i < overlapID.length; i ++){
                                    cat_Overlap_Area[i] ++
                                }
                            }

                        }
                    }

                    let Sum_Area = 0
                    let Overlap_Area = 0

                    for(let k in cat_Area){
                        Sum_Area += cat_Area[k]
                        Overlap_Area += cat_Overlap_Area[k]
                    }

                    console.log(filename)
                    console.log("OverLap Area: " + Overlap_Area)
                    console.log("Sum Area: " + Sum_Area)
                    console.log("Result: " + Overlap_Area / Sum_Area)

                    console.timeEnd("cal")



                })
        //}
        //let filename="life_fertility.txt"


    }


    render(){
        return (
            <div style={{width:1200,margin:"0 auto",marginTop:10}}>
                <svg id="mainsvg" width={1200} height={1200} style={{border:"1px solid #000000"}}></svg>
            </div>
        )
    }
}

export default CalOverLap