import * as d3 from "d3";

export function add(a,b){
    return a+b
}

export function calDistance(x1,y1,x2,y2){
    return Math.sqrt(Math.pow(x1-x2,2)+Math.pow(y1-y2,2))
}

export function getVecNorm(vec){
    let norm=Math.sqrt(Math.pow(vec[0],2)+Math.pow(vec[1],2))
    return norm
}

export function normalizeVec(vec){
    let norm=Math.sqrt(Math.pow(vec[0],2)+Math.pow(vec[1],2))
    let newvec=[vec[0]/norm,vec[1]/norm]
    return newvec
}

export function drawCircle(x,y,r,fill,stroke){
    d3.select("#mainsvg")
        .append("circle")
        .attr("class","elespace")
        .attr("cx",x)
        .attr("cy",y)
        .attr("r",r)
        .attr("fill",fill)
        .attr("stroke",stroke)
        .attr("stroke-width",1)
}

export function drawRect(x,y,width,height,color){
    d3.select("svg")
        .append("rect")
        .attr("x",x-width/2)
        .attr("y",y-height/2)
        .attr("width",width)
        .attr("height",height)
        .attr("fill","none")
        .attr("stroke",color)
        .attr("stroke-width",2)
}

export function drawPolygon(points,dx,dy,color){
    d3.select("svg").append("path")
        .style("fill", "none")
        .style("stroke",color)
        .style("stroke-width", 0.5)
        .attr("d", function () {
            var p = "";
            p += (d3.line()
                    .x(function (dat) {
                        return dat[0]+dx;
                    })
                    .y(function (dat) {
                        return dat[1]+dy;
                    })
            )(points)+ "";
            return p;
        })
        .on('mouseover', function () {
            d3.select(this)
                .style('stroke', "red")
                .style("stroke-width", 2);
        })
        .on('mouseout', function () {
            d3.select(this)
                .style('stroke', "black")
                .style("stroke-width", 0.5);
        });
}
export function drawLine(start,end,color,width){
    d3.select("svg").append("line")
        .attr("x1",start[0])
        .attr("y1",start[1])
        .attr("x2",end[0])
        .attr("y2",end[1])
        .attr("stroke-width",width)
        .attr("stroke",color)
        .on("mouseover",function(){
            d3.select(this)
                .attr("stroke","red")
        })
        .on("mouseout",function(){
            d3.select(this)
                .attr("stroke",color)
        })
}

export function  isContained(catarr,cat){
    catarr=catarr.split(",")
    for(let i=0;i<catarr.length;i++){
        if(catarr[i]==cat){
            return true
        }
    }
    return false
}

export function  PointToSegDist( x,  y,  x1,  y1,  x2,  y2)
{
     let cross = (x2 - x1) * (x - x1) + (y2 - y1) * (y - y1);
    // if (cross <= 0) return Math.sqrt((x - x1) * (x - x1) + (y - y1) * (y - y1));
    if (cross <= 0) return -1

     let d2 = (x2 - x1) * (x2 - x1) + (y2 - y1) * (y2 - y1);
    // if (cross >= d2) return Math.sqrt((x - x2) * (x - x2) + (y - y2) * (y - y2));
    if (cross >= d2) return -1



    let r = cross / d2;
    let px = x1 + (x2 - x1) * r;
    let py = y1 + (y2 - y1) * r;
    return Math.sqrt((x - px) * (x - px) + (py - y) * (py - y));
}

