    above(P,Vi,Vj)  {
        return this.isLeft(P,Vi,Vj) > 0
    }

    below(P,Vi,Vj)  {
        return this.isLeft(P,Vi,Vj) < 0
    }

    isLeft(P0, P1, P2) {
        return (P1.x - P0.x)*(P2.y - P0.y) - (P2.x - P0.x)*(P1.y - P0.y);
    }

    RLtangent_PolyPolyC(V, W) {
        let  ix1, ix2;      // search indices for polygons 1 and 2

        // first get the initial vertex on each polygon
        // ix1 = this.Rtangent_PointPolyC(W[0], V);   // right tangent from W[0] to V
        ix1=this.tangent_PointPoly(W[0],V)[1]
        // ix2 = this.Ltangent_PointPolyC(V[ix1], W); // left tangent from V[ix1] to W

        ix2=this.tangent_PointPoly(V[ix1],W)[0]

        // ping-pong linear search until it stabilizes
        let done = false                    // flag when done
        while (done == false) {
            done = true;                     // assume done until...
            while (this.isLeft(W[(ix2+W.length)%W.length], V[ix1], V[ix1+1]) <= 0){
                ++ix1;                       // get Rtangent from W[ix2] to V
            }

            while (this.isLeft(V[ix1], W[(ix2+W.length)%W.length], W[(ix2-1+W.length)%W.length]) >= 0){
                --ix2;                       // get Ltangent from V[ix1] to W
                done = false;                // not done if had to adjust this
            }
        }
        let t1 = ix1;
        let t2 = ix2;
        if(t1<0){
            t1=t1+V.length
        }
        if(t2<0){
            t2=t2+W.length
        }

        return[t1,t2]
    }

    drawSpacePathWithPolygon(){
        for (let i=0;i<this.elementPoly.length;i++){
            let espace=this.elementPoly[i]
            let nowp=espace[0]
            let nextp
            for (let e=1;e<espace.length;e++){
                nextp=espace[e]
                drawLine([nowp.x,nowp.y],[nextp.x,nextp.y],"black",1)
                nowp=nextp
            }
        }
    }

    approxArcWithPolygon(){
        for (let i=0;i<this.elementSpace.length;i++){
            let espace=this.elementSpace[i]
            for (let e=0;e<espace.length;e++){

                let center=espace[e].center
                let radius=espace[e].radius
                if(espace[e].type=="arc"){
                    let startpoint=espace[e].start
                    let endpoint=espace[e].end
                    // drawCircle(startpoint[0],startpoint[1],5,"blue","none")
                    // drawCircle(endpoint[0],endpoint[1],5,"green","none")

                    let startvec=[startpoint[0]-center[0],startpoint[1]-center[1]]
                    let endvec=[endpoint[0]-center[0],endpoint[1]-center[1]]
                    // let vecstart=[espace[e].]
                    let degree=getDegreeTwoVec(startvec,endvec)

                    let crossmul1=crossMul(startvec,endvec)
                    if(crossmul1>0){
                        degree=2*Math.PI-degree
                    }

                    let d=deltaDeg
                    let polySegs=[]

                    polySegs.push(startpoint)
                    while(d<degree){
                        //rotate the startpoint
                        let segpoint=rotatePoint(startpoint[0],startpoint[1],center[0],center[1],-1*d)
                        // drawCircle(segpoint[0],segpoint[1],2,"red","none")
                        polySegs.push(segpoint)
                        d+=deltaDeg
                    }
                    if(d!=degree){
                        polySegs.push(endpoint)
                    }

                    this.elementSpace[i][e].polysegment=polySegs
                }
                else if(espace[e].type=="circle"){
                    let startpoint=[center[0]-radius,center[1]]
                    let endpoint=startpoint
                    espace[e].start=startpoint
                    espace[e].end=endpoint
                    let startvec=[startpoint[0]-center[0],startpoint[1]-center[1]]
                    // drawCircle(startpoint[0],startpoint[1],5,"blue","none")
                    // drawCircle(endpoint[0],endpoint[1],5,"green","none")
                    let degree=Math.PI*2
                    let d=deltaDeg
                    let polySegs=[]
                    polySegs.push(startpoint)
                    while(d<=degree){
                        //rotate the startpoint
                        let segpoint=rotatePoint(startpoint[0],startpoint[1],center[0],center[1],-1*d)
                        // drawCircle(segpoint[0],segpoint[1],2,"red","none")
                        polySegs.push(segpoint)
                        d+=deltaDeg
                    }
                    if(d!=degree){
                        polySegs.push(endpoint)
                    }
                    this.elementSpace[i][e].polysegment=polySegs
                }
            }
        }

        for (let i=0;i<this.elementSpace.length;i++){
            let espace=this.elementSpace[i]
            let newespace=[]
            for (let e=0;e<espace.length;e++){
                if(espace[e].type=="line"){
                    newespace.push(espace[e])
                }
                else{
                    //arc segments
                    let nowp=espace[e].polysegment[0]
                    for (let s=1;s<espace[e].polysegment.length;s++){
                        let nextp=espace[e].polysegment[s]
                        newespace.push({
                            type:"line",
                            center:espace[e].center,
                            radius:re,
                            start:nowp,
                            end:nextp
                        })
                        nowp=nextp
                    }
                }
            }
            this.elementPoly.push(newespace)
        }

        for (let i=0;i<this.elementPoly.length;i++){
            let eseg=this.elementPoly[i]
            let newsegs=[]
            newsegs.push({
                x:eseg[0].start[0],
                y:eseg[0].start[1],
                degree:0
            })
            for (let e=0;e<eseg.length;e++){
                let point=eseg[e].end
                newsegs.push({
                    x:point[0],
                    y:point[1],
                    degree:0
                })
            }
            this.elementPoly[i]=newsegs
        }

        console.log(this.elementPoly)

    }

