    normalizeEdgeLength(){
        let maxDis=-1
        for (let i=0;i<this.tanGraphEdges.length;i++){
            if(this.tanGraphEdges[i].distance>maxDis){
                maxDis=this.tanGraphEdges[i].distance
            }
        }
        for (let i=0;i<this.tanGraphEdges.length;i++){
            this.tanGraphEdges[i].distance=this.tanGraphEdges[i].distance/maxDis
        }

    }

    checkEdgeCrossing(P1,P2,Q1,Q2){
        if(Math.min(P1.x,P2.x) <= Math.max(Q1.x,Q2.x) &&
            Math.min(Q1.x,Q2.x) <= Math.max(P1.x,P2.x) &&
            Math.min(P1.y,P2.y) <= Math.max(Q1.y,Q2.y) &&
            Math.min(Q1.y,Q2.y) <= Math.max(P1.y,P2.y)){
            if(
                ((Q1.x-P1.x)*(Q1.y-Q2.y)-(Q1.y-P1.y)*( Q1.x-Q2.x)) * ((Q1.x-P2.x)*(Q1.y-Q2.y)-(Q1.y-P2.y)*(Q1.x-Q2.x)) < 0 &&
                ((P1.x-Q1.x)*(P1.y-P2.y)-(P1.y-Q1.y)*(P1.x-P2.x)) * ((P1.x-Q2.x)*(P1.y-P2.y)-(P1.y-Q2.y)*( P1.x-P2.x)) < 0
            ){
                return true;
            }
            else{
                return false;
            }
        }
        else{
            return false
        }
    }

    calPathLength(path){
        let length=0
        for (let i=0;i<path.length;i++){
            let e=path[i]
            let startid=e.from()
            let endid=e.to()
            let startpoint=this.tanGraphPoints[startid]
            let endpoint=this.tanGraphPoints[endid]
            length+=calDistance(startpoint.x,startpoint.y,endpoint.x,endpoint.y)
        }
        return length
    }

