    centralizeData(){
        for (let i=0;i<this.pointData.length;i++){
            this.pointData.id=i
        }

        let minx=Number.MAX_VALUE
        let miny=Number.MAX_VALUE
        let maxx=Number.MIN_VALUE
        let maxy=Number.MIN_VALUE
        for (let i=0;i<this.pointData.length;i++){
            if (this.pointData[i].x>maxx)
                maxx=this.pointData[i].x
            if(this.pointData[i].y>maxy)
                maxy=this.pointData[i].y
            if(this.pointData[i].x<minx)
                minx=this.pointData[i].x
            if(this.pointData[i].y<miny)
                miny=this.pointData[i].y
        }

        /*        let centerx=minx+(maxx-minx)/2
                let centery=miny+(maxy-miny)/2
                let move=[this.canvasWidth/2-centerx,this.canvasHeight/2-centery]

                for (let i=0;i<this.pointData.length;i++){
                    this.pointData[i].x+=move[0]
                    this.pointData[i].y+=move[1]
                }*/
        this.maxX=maxx
        this.minX=minx
        this.maxY=maxy
        this.minY=miny
        //console.log(this.minX,this.maxX,this.minY,this.maxY)

        d3.select("#mainsvg")
            .attr("viewBox",(minx-40)+" "+(miny-40)+" "+(maxx-minx+80)+" "+(maxy-miny+80))


        /*        for (let i=0;i<this.pointData.length;i++){
                    this.pointData[i].x-=this.minX-20
                    this.pointData[i].y-=this.minY-20
                }*/

        let offsetX = minx-20;
        let offsetY = miny-20;
        let viewWidth = maxx-minx+40;
        let viewHeight = maxy-miny+40

        for (let i=0;i<this.pointData.length;i++){
            this.pointData[i].x=parseFloat(this.pointData[i].x)
            this.pointData[i].y=parseFloat(this.pointData[i].y)
            this.pointData[i].id=i
            this.pointData[i].cat=this.pointData[i].cat.toString()
            let cats=this.pointData[i].cat.split(",")
            let catarray=[]
            if (cats.length>1){
                this.pointData[i].multicat=true
                for (let c=0;c<cats.length;c++){
                    catarray.push(cats[c])
                }
            }
            else{
                this.pointData[i].multicat=false
                catarray.push(this.pointData[i].cat)
            }
            this.pointData[i].catArray=catarray
            for (let c=0;c<this.pointData[i].catArray.length;c++){
                let nowcat=this.pointData[i].catArray[c]
                if(!this.catPointDict.hasOwnProperty(nowcat)){
                    this.catPointDict[nowcat]=[]
                }
                this.catPointDict[nowcat].push(this.pointData[i])
            }
        }

        // console.log(this.catPointDict)
    }

