    checkEdgeCrossing(P1,P2,Q1,Q2){
        if(Math.min(P1.x,P2.x) <= Math.max(Q1.x,Q2.x) &&
            Math.min(Q1.x,Q2.x) <= Math.max(P1.x,P2.x) &&
            Math.min(P1.y,P2.y) <= Math.max(Q1.y,Q2.y) &&
            Math.min(Q1.y,Q2.y) <= Math.max(P1.y,P2.y)){
            if(
                ((Q1.x-P1.x)*(Q1.y-Q2.y)-(Q1.y-P1.y)*( Q1.x-Q2.x)) * ((Q1.x-P2.x)*(Q1.y-Q2.y)-(Q1.y-P2.y)*(Q1.x-Q2.x)) < 0 &&
                ((P1.x-Q1.x)*(P1.y-P2.y)-(P1.y-Q1.y)*(P1.x-P2.x)) * ((P1.x-Q2.x)*(P1.y-P2.y)-(P1.y-Q2.y)*( P1.x-P2.x)) < 0
            ){
                return true;
            }
            else{
                return false;
            }
        }
        else{
            return false
        }
    }

    calCrossEdges(){
        console.log(this.SPGEdges)
        var Edge_Array = {}
        for(let k in this.SPGEdges){
            Edge_Array[k] = []
            for(let i = 0; i < this.SPGEdges[k].length; i ++){
                let idx = this.SPGEdges[k][i]
                let edge_path = this.catInitEdges[k][idx].edges
                let tmp_e = []
                for(let p = 0; p < edge_path.length; p ++){
                    let tmp_line = {}
                    let u = edge_path[p].v
                    let v = edge_path[p].w
                    tmp_line.start = {x: this.tanGraphPoints[u].x,
                                      y:this.tanGraphPoints[u].y}
                    tmp_line.end = {x:this.tanGraphPoints[v].x,
                                    y:this.tanGraphPoints[v].y}
                    tmp_e.push(tmp_line)
                }
                Edge_Array[k].push(tmp_e)
            }
        }

        let crossnum = 0
        for(let k1 in Edge_Array){

            for(let i = 0; i < Edge_Array[k1].length; i ++){
                let e1 = Edge_Array[k1][i]
                for(let k2 in Edge_Array){
                    if(k1 == k2){
                        continue
                    }
                    for(let j = 0; j < Edge_Array[k2].length; j ++){
                        let e2 = Edge_Array[k2][j]
                        let flag = false
                        for(let p = 0; p < e1.length; p ++){
                            let eS1 = e1[p].start
                            let eE1 = e1[p].end
                            for(let q = 0; q < e2.length; q ++){
                                let eS2 = e2[q].start
                                let eE2 = e2[q].end
                                if(this.checkEdgeCrossing(eS1, eE1, eS2, eE2)){
                                    crossnum ++
                                    flag = true
                                    break
                                }
                            }
                            if(flag){

                                for(let q = 0; q < e2.length; q ++){
                                    let eS2 = e2[q].start
                                    let eE2 = e2[q].end
                                    let startpoints = [eS2.x, eS2.y]
                                    let endpoints = [eE2.x, eE2.y]
                                    drawLine(startpoints, endpoints, "red", 2)
                                }

                                break
                            }
                        }
                    }
                }
            }

        }

        crossnum /= 2

        console.log("Points Num : ")
        console.log(this.pointData.length)
        console.log("Cross Num : ")
        console.log(crossnum)

    }

