    drawEdgePath(k, i, color, width){
        let edge_path = this.catInitEdges[k][i].edges
        for(let p = 0; p < edge_path.length; p ++){
            let u = edge_path[p].v
            let v = edge_path[p].w
            let start_points = [this.tanGraphPoints[u].x, this.tanGraphPoints[u].y]
            let end_points = [this.tanGraphPoints[v].x, this.tanGraphPoints[v].y]
            drawLine(start_points, end_points, color, width)
        }
    }

    judgeClockWise(pointIdx){

        //counterclockwise return true
        //clockwise return false
        let p1 = [this.pointData[pointIdx[0]].x, this.pointData[pointIdx[0]].y]
        let p2 = [this.pointData[pointIdx[1]].x, this.pointData[pointIdx[1]].y]
        let p3 = [this.pointData[pointIdx[2]].x, this.pointData[pointIdx[2]].y]
        let res = (p2[0] - p1[0]) * (p3[1] - p1[1]) - (p2[1] - p1[1]) * (p3[0] - p1[0])
        if(res > 0){
            return true
        }
        else{
            return false
        }

    }

    dfs(s, k){
        this.visited[s] = 1
        for(let i = 0; i < this.SPG[k].adjList[s].length; i ++){
            let e = this.SPG[k].adjList[s][i]
            let to = e.w
            if(this.visited[to] == 0){
                this.fa[to] = s
                this.dfs(to, k)
            }
            else if(this.visited[to] == 1 && to != this.fa[s]){
                let tmp = s
                let tmp_circle = []
                tmp_circle.push(to)
                while(tmp != to){
                    tmp_circle.push(tmp)
                    tmp = this.fa[tmp]
                }
                //tmp_circle.push(tmp)
                if(tmp_circle.length < 3){
                    continue
                }
                if(!this.circles.hasOwnProperty(k)){
                    this.circles[k] = []
                }
                if(!this.judgeClockWise(tmp_circle)){
                    tmp_circle.reverse()
                }
                this.circles[k].push(tmp_circle)
                this.circles[k][this.circles[k].length - 1].delflag = false
            }
        }
        this.visited[s] = 2
    }

