    searchFather(x){
        while(x!=this.f[x]){
            this.f[x] = this.f[this.f[x]];
            x = this.f[x];
        }
        return x;
    }

    unionFather(x,y){
        this.f[this.searchFather(y)] = this.searchFather(x);
    }

