    sortEdgesGlobal(){
        function sortEdgesfunc(a,b){
            return a[2]-b[2]
        }
        let edges=[]
        let disArray=[]
        let messArray=[]
        let tempedges=[]
        let maxDis=Number.MIN_VALUE
        for (let k in this.catPointDict){
            for(let i=0;i<this.catPointDict[k].length;i++){
                for (let j=i+1;j<this.catPointDict[k].length;j++){
                    let dis=this.calDistance(this.catPointDict[k][i],this.catPointDict[k][j])
                    let mess=this.calEdgeMess(this.catPointDict[k][i],this.catPointDict[k][j])
                    disArray.push(dis)
                    messArray.push(mess)
                    if(dis>maxDis){
                        maxDis=dis
                    }
                    tempedges.push([this.catPointDict[k][i].id,this.catPointDict[k][j].id,dis,mess])
                }
            }
        }
        this.maxDistance=maxDis
        for (let i=0;i<tempedges.length;i++){
            let start=tempedges[i][0]
            let end=tempedges[i][1]
            let dis=tempedges[i][2]/maxDis
            let mess=tempedges[i][3]
            let weight=dis*(mess+1)
            // let weight=dis
            edges.push([start,end,weight,dis,mess])
        }

        edges.sort(sortEdgesfunc)
        return edges
    }

