    checkConnectBetweenAllClusterV2(){
        for(let k in this.clusterDict){
            let m = this.clustergraphInfo[k].edgesnum;
            let n = this.clustergraphInfo[k].nodesnum;
            if (m != n - 1){
                return false;
            }
        }
        return true
    }

    checkConnectBetweenAllCluster(){
        for(let k in this.clusterDict){
            let start=this.clusterDict[k][0].id
            var dfs = new jsgraphs.DepthFirstSearch(this.clustergraph, start);
            for (let i=1;i<this.clusterDict[k].length;i++){
                if (!dfs.hasPathTo(this.clusterDict[k][i].id)){
                    return false
                }
            }
        }
        return true
    }


    checkConnectBetweenTwoClusterV2(id1,id2){
        return (this.searchFather(id1)==this.searchFather(id2));
    }

    checkConnectBetweenTwoCluster(id1,id2){
        var dfs = new jsgraphs.DepthFirstSearch(this.clustergraph, id1);
        if (!dfs.hasPathTo(id2)){
            return false
        }
        else{
            return true
        }
    }
    checkConnect(){
        // this.drawPointData()
        // this.drawEdges()
        for(let k in this.catPointDict){
            let start=this.catPointDict[k][0].id
            var dfs = new jsgraphs.DepthFirstSearch(this.graph, start);
            for (let i=1;i<this.catPointDict[k].length;i++){
                if (!dfs.hasPathTo(this.catPointDict[k][i].id)){
                    return false
                }
            }
        }
        return true
    }

    sortRemoveEdges(removelist){
        function sortRemove(a,b){
            return a.weight-b.weight
        }

        removelist.sort(sortRemove())
        return removelist
    }

    calEdgeWeightWithCrossing(node1,node2){
        let dis=this.calDistance(node1,node2)
        let mess=this.calEdgeMess(node1,node2)

        let crossnum=0
        for (let i=0;i<this.edges.length;i++){
            let start=this.edges[i].start
            let end=this.edges[i].end
            let edgecat=this.pointData[start].cat
            if(node1.cat!=edgecat&&this.checkEdgeCrossing(node1,node2,this.pointData[start],this.pointData[end])){
                crossnum+=1
            }
        }

        return [dis*(mess+1)*(crossnum+1)/this.maxDistance,dis/this.maxDistance,mess,crossnum]

    }

    calEdgeWeightWithCrossing2(node1,node2){
        let dis=this.calDistance(node1,node2)
        let mess=this.calEdgeMess(node1,node2)

        let crossnum=0
        for (let i=0;i<this.edges.length;i++){
            let start=this.edges[i].start
            let end=this.edges[i].end
            let edgecat=this.pointData[start].cat
            if(node1.cat!=edgecat&&this.checkEdgeCrossing(node1,node2,this.pointData[start],this.pointData[end])){
                crossnum+=1
            }
        }

        return [dis*(mess+1)*(crossnum+1)/this.maxDistance,dis/this.maxDistance,mess,crossnum]

    }

    calDistance(node1,node2){
        return Math.sqrt(Math.pow(node1.x-node2.x,2)+Math.pow(node1.y-node2.y,2))
    }

    calEdgeMess(node1,node2){
        let num=0
        for (let i=0;i<this.pointData.length;i++){
            if(this.pointData[i].cat!=node1.cat&&this.pointData[i].cat!=node2.cat){
                if(this.isPointInEdgeRange(node1,node2,this.pointData[i])){
                    let pointedgedis=this.calPointEdgeDistance(node1,node2,this.pointData[i])
                    // console.log(pointedgedis)
                    if(pointedgedis<POINT_EDGE_EPS){
                        num+=1
                    }
                }
            }
        }
        // console.log(num)
        return num
    }

    calEdgeWeight(node1,node2){
        let dis=this.calDistance(node1,node2)
        let mess=this.calEdgeMess(node1,node2)
        return [dis,mess]
    }

    getVecNorm(vec){
        return Math.sqrt(Math.pow(vec[0],2)+Math.pow(vec[1],2))
    }

    calVecCosine(vec1,vec2){
        let dot=vec1[0]*vec2[0]+vec1[1]*vec2[1]
        let norm1=this.getVecNorm(vec1)
        let norm2=this.getVecNorm(vec2)
        return dot/(norm1*norm2)

    }
    isPointInEdgeRange(start,end,point){
        let vec1=[point.x-start.x,point.y-start.y]
        let vec2=[end.x-start.x,end.y-start.y]
        let cos1=this.calVecCosine(vec1,vec2)
        if(cos1<0){
            return false
        }

        let vec3=[point.x-end.x,point.y-end.y]
        let vec4=[start.x-end.x,start.y-end.y]
        let cos2=this.calVecCosine(vec3,vec4)

        if(cos2<0){
            return false
        }

        return true
    }

    calPointEdgeDistance(start,end,point){
        //must check if the point is in the range of the edge before calling this function
        let vec1=[end.x-start.x,end.y-start.y]
        let vec2=[point.x-start.x,point.y-start.y]

        let dot=vec1[0]*vec2[0]+vec1[1]*vec2[1]
        let seg1=dot/this.getVecNorm(vec1)
        let norm=this.getVecNorm(vec2)

        return Math.sqrt(Math.pow(norm,2)-Math.pow(seg1,2))
    }

