    checkCircle(k){
        let catEdges=[]
        let nodeDegree={}
        for (let i=0;i<this.edges.length;i++){
            if(this.edges[i].cat==k){
                catEdges.push(this.edges[i])
                let start=this.edges[i].start
                let end=this.edges[i].end
                if(!nodeDegree.hasOwnProperty(start)){
                    nodeDegree[start]=[]
                }
                nodeDegree[start].push(i)
                if(!nodeDegree.hasOwnProperty(end)){
                    nodeDegree[end]=[]
                }
                nodeDegree[end].push(i)
            }
        }
        let finish=false
        while(!finish){
            finish=true
            for(var p in nodeDegree){
                if(nodeDegree[p].length==1){
                    finish=false
                    let index=nodeDegree[p][0]
                    let start=this.edges[index].start
                    let end=this.edges[index].end
                    for (let i=0;i<nodeDegree[start].length;i++){
                        if(nodeDegree[start][i]==index){
                            nodeDegree[start].splice(i,1)
                            break
                        }
                    }
                    for (let i=0;i<nodeDegree[end].length;i++){
                        if(nodeDegree[end][i]==index){
                            nodeDegree[end].splice(i,1)
                            break
                        }
                    }
                }
            }
        }
        let circleEdges=[]
        for (var p in nodeDegree){
            if(nodeDegree[p].length>0){
                for (let i=0;i<nodeDegree[p].length;i++){
                    let edgeindex=nodeDegree[p][i]
                    let has=false
                    for (let j=0;j<circleEdges.length;j++){
                        if(circleEdges[j]==edgeindex){
                            has=true
                            break
                        }
                    }
                    if(!has){
                        circleEdges.push(edgeindex)
                    }
                }
            }

        }
        // console.log(nodeDegree,circleEdges)
        return circleEdges
    }

    findCircle(lastcat){
        let that=this
        let circleEdges=that.checkCircle(lastcat)
        if(circleEdges.length>0){
            let scores=[]
            for (let i=0;i<circleEdges.length;i++){
                let index=circleEdges[i]

                let start=that.pointData[that.edges[index].start]
                let end=that.pointData[that.edges[index].end]
                let crossnum=0
                if(that.isAdded(that.edges[index].start,that.edges[index].end)){
                    scores.push(-1)
                }
                else{
                    for (let e=0;e<that.edges.length;e++){
                        let e1=that.pointData[that.edges[e].start]
                        let e2=that.pointData[that.edges[e].end]
                        if(e1.cat!=start.cat&&e1.cat!=end.cat&&that.checkEdgeCrossing(start,end,e1,e2)){
                            crossnum+=1
                        }
                    }
                    //cal distance
                    let distance=that.calDistance(start,end)
                    //cal num of other points
                    let mess=that.calEdgeMess(start,end)
                    let score=weightCross*crossnum+weightDistance*distance/that.maxDis+weightMess*mess
                    scores.push(score)
                }

                /*                                    d3.select(svgid)
                                                        .select("#edgeGroup")
                                                        .selectAll("line")
                                                        .filter(function(){
                                                            return parseInt(d3.select(this).attr("index"))==index
                                                        })
                                                        .attr("stroke","blue")
                                                        .on("mouseover",function(){
                                                            console.log(scores[i])
                                                        })*/
            }

            let worstindex=-1
            let worstscore=-100
            for (let i=0;i<scores.length;i++){
                if(scores[i]>worstscore){
                    worstscore=scores[i]
                    worstindex=i
                }
            }
            let worstEdge=that.edges[circleEdges[worstindex]]

            //let tmpe = clone(worstEdge)
            //tmpe.startpoint = clone(worstEdge.start)
            //tmpe.endpoint = clone(worstEdge.end)
            //tmpe.flag = false
            //tmpEditEdges.push(tmpe)


            //console.log("WorstEdge: ")
            //console.log(circleEdges)
            //console.log(worstEdge)
            //console.log(that.edges)

            d3.selectAll("line")
                .filter(function(){
                    return parseInt(d3.select(this).attr("index"))==worstEdge.id
                })
                .attr("stroke","red")
                .attr("stroke-width",5)
            // console.log(weightMess)

        }
    }

