    countEdgeCrossingWithRouting(){
        let count=0
        for(let i=0;i<this.edges.length;i++){
            let lines1=[]
            let start1=this.pointData[this.edges[i].start]
            let end1=this.pointData[this.edges[i].end]
            if(this.edges[i].virtrual){
                let vnodes=this.edges[i].virtrualNodes
                lines1.push([start1.x,start1.y])
                for (let p=0;p<vnodes.length;p++){
                    lines1.push(vnodes[p])
                }
                lines1.push([end1.x,end1.y])
            }
            else{
                lines1.push([start1.x,start1.y])
                lines1.push([end1.x,end1.y])
            }
            for (let j=i+1;j<this.edges.length;j++){
                let lines2=[]
                let start2=this.pointData[this.edges[j].start]
                let end2=this.pointData[this.edges[j].end]
                if(start1.cat==start2.cat){
                    continue
                }
                if(this.edges[j].virtrual){
                    let vnodes=this.edges[j].virtrualNodes
                    lines2.push([start2.x,start2.y])
                    for (let p=0;p<vnodes.length;p++){
                        lines2.push(vnodes[p])
                    }
                    lines2.push([end2.x,end2.y])
                }
                else{
                    lines2.push([start2.x,start2.y])
                    lines2.push([end2.x,end2.y])
                }
                for (let l1=0;l1<lines1.length-1;l1++){
                    let p1={x:lines1[l1][0],y:lines1[l1][1]}
                    let p2={x:lines1[l1+1][0],y:lines1[l1+1][1]}
                    for (let l2=0;l2<lines2.length-1;l2++){
                        let p3={x:lines2[l2][0],y:lines2[l2][1]}
                        let p4={x:lines2[l2+1][0],y:lines2[l2+1][1]}
                        if(this.checkEdgeCrossing(p1,p2,p3,p4)){
                            count+=1
                        }
                    }
                }


            }



        }

        console.log("num of edge crossing:" +count)
    }

    countEdgeCrossing(){
        let count=0
        for(let i=0;i<this.edges.length;i++){
            let start1=this.pointData[this.edges[i].start]
            let end1=this.pointData[this.edges[i].end]
            for (let j=i+1;j<this.edges.length;j++){
                let start2=this.pointData[this.edges[j].start]
                let end2=this.pointData[this.edges[j].end]
                if(start1.cat!=start2.cat){
                    if(this.checkEdgeCrossing(start1,end1,start2,end2)){
                        count+=1
                    }
                }
            }
        }
        console.log("num of edge crossing: "+count)
    }

    createLocalTree(points){

        //1. find center point
        //2. generate tree
        let midx=0
        let midy=0
        for (let i=0;i<points.length;i++){
            midx+=points[i].x
            midy+=points[i].y
        }
        midx=midx/points.length
        midy=midy/points.length
        let midnode={
            x:midx,
            y:midy
        }
        let minDis=Number.MAX_VALUE
        let minindex=-1
        for (let i=0;i<points.length;i++){
            let dis=this.calDistance(midnode,points[i])
            if(dis<minDis){
                minDis=dis
                minindex=i
            }
        }



        for (let i=0;i<points.length;i++){
            if(i!=minindex){
                this.edges.push({
                    start:points[minindex].id,
                    end:points[i].id,
                    virtrual:false,
                    virtrualNodes:[],
                    cat:points[i].cat,
                    isLocalTree:true
                })
                this.graph.addEdge(points[minindex].id,points[i].id)
            }
        }



        // console.log(this.edges)
        /*        var g = new jsgraphs.Graph(this.pointData.length);
                for (let i=0;i<g.V;i++){
                    g.node(i).label=this.pointData[i].cat
                }*/

    }

