    generateTreeWithHierarchical(){
        // this.findCluster()
        this.initWithNoCluster()
        //merging clusters according to this.edges

        let preAddedClusters=[]
        for (let i=0;i<this.edges.length;i++){
            let start=this.pointData[this.edges[i].start]
            let end=this.pointData[this.edges[i].end]
            let cat=start.cat
            let catclusters=this.clusterDict[cat]
            let startclusterid=-1
            let endclusterid=-1
            for(let c=0;c<catclusters.length;c++){
                for (let p=0;p<catclusters[c].length;p++){
                    if(catclusters[c][p].id==start.id){
                        startclusterid=c
                    }
                    if(catclusters[c][p].id==end.id){
                        endclusterid=c
                    }
                    if(startclusterid!=-1&&endclusterid!=-1){
                        break
                    }
                }
                if(startclusterid!=-1&&endclusterid!=-1){
                    break
                }

            }
            if(startclusterid!=-1&&endclusterid!=-1&&startclusterid!=endclusterid){
                // console.log(this.clusterDict[cat].length)

                //merge two cluster
                let merged=catclusters[startclusterid]
                for(let p=0;p<catclusters[endclusterid].length;p++){
                    merged.push(catclusters[endclusterid][p])
                }
                let newclusterdict=[]
                for (let c=0;c<catclusters.length;c++){
                    if(c!=startclusterid&&c!=endclusterid){
                        newclusterdict.push(catclusters[c])
                    }
                }
                newclusterdict.push(merged)
                this.clusterDict[cat]=newclusterdict
                preAddedClusters.push([cat,newclusterdict.length-1])
                console.log(cat)
            }

        }

        this.createGraphWithCluster()

        for (var k in this.clusterDict){
            this.clustergraphInfo[k] = {edgesnum:0,nodesnum:this.clusterDict[k].length};
            for (let i=0;i<this.clusterDict[k].length;i++){
                let iscontinue=false
                for (let c=0;c<preAddedClusters.length;c++){
                    let cat=preAddedClusters[c][0]
                    let id=preAddedClusters[c][1]
                    if(k==cat&&id==i){
                        iscontinue=true
                    }
                }
                if(iscontinue){
                    continue
                }
                else{
                    this.createLocalTree(this.clusterDict[k][i])
                }
            }
        }
        // console.log(this.clusterDict)
        // console.log(this.edges.length)

        this.drawEdges()
        this.hierarchicalMergeV2()
        // console.log(this.edges.length)
    }

