    createGraphWithCluster(){
        let clusterArr=[]
        let idnum=0
        for (var k in this.clusterDict){
            for (let i=0;i<this.clusterDict[k].length;i++){
                clusterArr.push({
                    points:this.clusterDict[k][i],
                    cat:k,
                    id:idnum
                })
                this.clusterDict[k][i].id=idnum
                idnum+=1


            }

        }
        this.clusterArr=clusterArr
        //console.log(this.clusterDict)
        var g = new jsgraphs.Graph(this.clusterArr.length);
        for (let i=0;i<g.V;i++){
            g.node(i).label=this.clusterArr[i].cat
        }
        // console.log(g.V,this.clusterArr.length)

        this.clustergraph=g
        console.log(g.V)

        var g2 = new jsgraphs.Graph(this.pointData.length);
        for (let i=0;i<g2.V;i++){
            g2.node(i).label=this.pointData[i].cat
        }
        this.graph=g2
        //console.log(g,g2)
        for (let i=0;i<this.edges.length;i++){
            this.graph.addEdge(this.edges[i].start,this.edges[i].end)
        }

    }

    calClusterDistance(cluster1,cluster2){
        let dissum=0
        let num=0
        let mindis=Number.MAX_VALUE
        for(let i=0;i<cluster1.length;i++){
            for (let j=0;j<cluster2.length;j++){
                // dissum+=this.calDistance(cluster1[i],cluster2[j])
                // num+=1
                let dis=this.calDistance(cluster1[i],cluster2[j])
                if(dis<mindis){
                    mindis=dis
                }
            }
        }
        return mindis
    }

    sortEdgesGlobalWithCluster(){
        function sortEdgesfunc(a,b){
            return a[2]-b[2]
        }

        let edges=[]
        let disArray=[]
        let messArray=[]
        let tempedges=[]
        let maxDis=Number.MIN_VALUE
        for (let k in this.clusterDict){
            for(let i=0;i<this.clusterDict[k].length;i++){
                for (let j=i+1;j<this.clusterDict[k].length;j++){
                    let dis=this.calClusterDistance(this.clusterDict[k][i],this.clusterDict[k][j])
                    // let mess=this.calEdgeMess(this.clusterDict[k][i],this.clusterDict[k][j])
                    disArray.push(dis)
                    // messArray.push(mess)
                    if(dis>maxDis){
                        maxDis=dis
                    }
                    // tempedges.push([this.catPointDict[k][i].id,this.catPointDict[k][j].id,dis,mess])
                    tempedges.push([this.clusterDict[k][i].id,this.clusterDict[k][j].id,dis])
                }
            }
        }
        this.maxDistance=maxDis
        for (let i=0;i<tempedges.length;i++){
            let start=tempedges[i][0]
            let end=tempedges[i][1]
            let dis=tempedges[i][2]/maxDis
            // let mess=tempedges[i][3]
            // let weight=dis*(mess+1)
            let weight=dis
            // edges.push([start,end,weight,dis,mess])
            edges.push([start,end,weight,dis])
        }

        edges.sort(sortEdgesfunc)
        return edges
    }

    findLinkBetweenCluster(cluster1,cluster2){
        let mindis=Number.MAX_VALUE
        let minstart=null
        let minend=null
        let flag=false
        for (let i=0;i<cluster1.length;i++){
            let start=cluster1[i]
            for(let j=0;j<cluster2.length;j++){
                let end=cluster2[j]
                if(!this.checkNewEdge(start,end)){
                    flag=true
                    let dis=this.calDistance(start,end)
                    if(dis<mindis){
                        mindis=dis
                        minstart=start
                        minend=end
                    }
                }
            }
        }

        return [flag,minstart,minend,mindis]
    }

