    findOptimalLink(cluster1,cluster2){
        let minScore=Number.MAX_VALUE
        let bestStart=null
        let bestEnd=null

        let minScoreWithDis=Number.MAX_VALUE
        let bestStartWithDis=null
        let bestEndWithDis=null

        let hasBest=false
        for (let i=0;i<cluster1.length;i++){
            let start=cluster1[i]
            for(let j=0;j<cluster2.length;j++){
                let end=cluster2[j]

                //cal cross num
                let crossnum=0
                for (let e=0;e<this.edges.length;e++){
                    let e1=this.pointData[this.edges[e].start]
                    let e2=this.pointData[this.edges[e].end]
                    if(e1.cat!=start.cat&&e1.cat!=end.cat&&this.checkEdgeCrossing(start,end,e1,e2)){
                        crossnum+=1
                    }
                }

                //cal distance
                let distance=this.calDistance(start,end)


                //cal num of other points
                let mess=this.calEdgeMess(start,end)

                if(crossnum>maxCross){
                    maxCross=crossnum
                }

                let score=weightCross*crossnum+weightDistance*distance/this.maxDis+weightMess*mess
                if(score<minScore){
                    bestStart=start
                    bestEnd=end
                    minScore=score
                }
                if(distance<DISTANCE_THRESHHOLD){
                    if(score<minScoreWithDis){
                        bestStartWithDis=start
                        bestEndWithDis=end
                        minScoreWithDis=score
                        hasBest=true
                    }
                }

            }
        }

        /*        if(hasBest){
                    // console.log("edge under threshhold")
                    return [bestStartWithDis,bestEndWithDis,minScoreWithDis,this.calDistance(bestStartWithDis,bestEndWithDis)]
                }
                else{
                    // console.log("no edge under threshhold")
                    return [bestStart,bestEnd,minScore,this.calDistance(bestStart,bestEnd)]
                }*/
        return [bestStart,bestEnd,minScore,this.calDistance(bestStart,bestEnd)]
    }

