    findMinScoreTest(links){
        let minScore=Number.MAX_VALUE
        let minIndex=-1
        let minScoreWithDistance=Number.MAX_VALUE
        let minIndexWithDistance=-1
        let hasBest=false
        for(let i=links.length-1;i>=0;i--) {
            let id1 = links[i].clusterid1
            let id2 = links[i].clusterid2
            if(this.checkConnectBetweenTwoCluster(id1,id2)){
                continue;
            }
            if(links[i].score<minScore){
                minScore=links[i].score
                minIndex=i
            }
            if(links[i].distance<DISTANCE_THRESHHOLD&&links[i].score<minScoreWithDistance){
                minScoreWithDistance=links[i].score
                minIndexWithDistance=i
                hasBest=true
            }
        }

        if(hasBest){
            // console.log("yes")
            return links[minIndexWithDistance]
        }
        else{
            // console.log("no")
            return links[minIndex]

        }

        // return links[minIndex]

    }

