import ReactDOM from 'react-dom';
import React, { Component } from 'react'

import * as d3 from "d3"

var colors=['#f700ff','#fbb132','#0885c2','#001aff','#7b28b7','#1c8b3c','#ed334e']

class CSVData extends Component{
    constructor() {
        super();
        this.population=null
        this.lifeExp=null
        this.fertility=null
        this.geoInfo=null
        this.data=[]
        this.pointData=[]
    }

    componentDidMount() {
        this.getPopulation()
    }

    getPopulation(){
        let that=this

        d3.csv("data/GapMinder/population_total.csv",function(data){
            return {
                country:data.country,
                value:data.value
            }
        })
            .then(function(data){
                that.population=data
                console.log("population")
                console.log(that.population)
                that.getLifeExpectancy()
            })
    }

    getLifeExpectancy(){
        let that=this
        d3.csv("data/GapMinder/life_expectancy_years.csv",function(data){
            return {
                country:data.country,
                value:data.value
            }
        })
            .then(function(data){
                that.lifeExp=data
                console.log("lifeexp")
                console.log(that.lifeExp)
                that.getFertility()
            })
    }

    getFertility(){
        let that=this

        d3.csv("data/GapMinder/children_per_woman_total_fertility.csv",function(data){
            return {
                country:data.country,
                value:data.value
            }
        })
            .then(function(data){
                that.fertility=data
                console.log("fertility")
                console.log(that.fertility)
                that.getGroInfo()
            })
    }

    getGroInfo(){
        let that=this

        d3.csv("data/GapMinder/ddf--entities--geo--country.csv",function(data){
            return {
                country:data.name,
                value:data.worldregion
            }
        })
            .then(function(data){
                that.geoInfo=data
                console.log(that.geoInfo)
                that.dataFusion()
            })
    }

    dataFusion(){
        let datapoint=[]
        let countrylist={}
        let catnum=0
        let catDict={}

        for (let i=0;i<this.fertility.length;i++){
            if(this.fertility[i].value==""){
                continue
            }
            let country=this.fertility[i].country
            let hascat=false

            for (let j=0;j<this.geoInfo.length;j++){
                if(!catDict.hasOwnProperty(this.geoInfo[j].value)){
                    catDict[this.geoInfo[j].value]=catnum
                    catnum+=1
                }
                if(country==this.geoInfo[j].country){
                    // datapoint[i].geo=this.geoInfo[j].value
                    // datapoint[i].cat=catDict[this.geoInfo[j].value]
                    hascat=true
                    break
                }
            }
            if(!hascat){
                console.log(this.fertility[i].country)
                continue
            }
            else{
                datapoint.push({
                    country:this.fertility[i].country,
                    fertility:parseFloat(this.fertility[i].value)
                })
            }

        }

        for (let i=0;i<datapoint.length;i++){
            let country=datapoint[i].country

            for (let j=0;j<this.population.length;j++){
                if(country==this.population[j].country){
                    datapoint[i].population=parseInt(this.population[j].value)
                    break
                }
            }
            for (let j=0;j<this.lifeExp.length;j++){
                if(country==this.lifeExp[j].country){
                    datapoint[i].lifeExp=parseFloat(this.lifeExp[j].value)
                    break
                }
            }
            for (let j=0;j<this.geoInfo.length;j++){
                if(country==this.geoInfo[j].country){
                    datapoint[i].geo=this.geoInfo[j].value
                    datapoint[i].cat=catDict[this.geoInfo[j].value]
                    break
                }
            }


        }


        this.data=datapoint
        // console.log(JSON.stringify(datapoint))
        console.log(datapoint)
        this.drawScatterPlot()

    }

    savePointData(){
        let that=this
        d3.selectAll("circle")
            .each(function(){
                let x=d3.select(this).attr("cx")
                let y=d3.select(this).attr("cy")
                let index=d3.select(this).attr("index")
                that.pointData[index].x=parseFloat(x)
                that.pointData[index].y=parseFloat(y)
            })
        console.log(that.pointData)
        console.log(JSON.stringify(that.pointData))
    }

    drawPoint(x,y,r,color,cat,index){

        function dragged(event) {
            // console.log("end!")
            d3.select(this)
                .attr("cx",event.x)
                .attr("cy",event.y)
        }
        function dragstart() {
            // console.log("start!")
            // d3.select(this).classed("fixed", true);
        }

        var drag = d3.drag()
            .on("start",dragstart)
            .on("drag",dragged)

        let that=this
        d3.select("svg")
            .append("circle")
            .attr("cx",x)
            .attr("cy",y)
            .attr("r",5)
            .attr("fill",color)
            .attr("catefory",cat)
            .attr("index",index)
            .attr("stroke","black")
            .on("mouseover",function(d,i){
                console.log(that.data[index])
            })
            .call(drag)
        this.pointData.push({
            x:x,
            y:y,
            cat:cat,
            id:index,
            shape:"circle",
            radius:r,
            width:r,
            height:r
        })
    }

    drawScatterPlot(){


        let xscale=d3.scaleLinear().domain([d3.min(this.data.map((d)=>{return parseFloat(d.fertility)})),d3.max(this.data.map((d)=>{return d.fertility}))])
            .range([0,800])
        let yscale=d3.scaleLinear().domain([d3.min(this.data.map((d)=>{return parseFloat(d.lifeExp)})),d3.max(this.data.map((d)=>{return d.lifeExp}))])
            .range([1000,0])
        let rscale=d3.scaleSqrt().domain([d3.min(this.data.map((d)=>{return d.population})),d3.max(this.data.map((d)=>{return d.population}))])
            .range([3,20])

        for (let i=0;i<this.data.length;i++){
            this.data[i].id=i
            this.drawPoint(xscale(this.data[i].fertility),yscale(this.data[i].lifeExp),rscale(this.data[i].population),colors[this.data[i].cat],this.data[i].cat,i)
        }


        this.savePointData()
    }

    getCSV(){


    }



    render(){
        return (<div id={"svgcontainer"}>
            <svg id={"mainsvg"} width={"1000px"} height={"1000px"}>
            </svg>
            <button onClick={this.savePointData.bind(this)}> Save </button>
        </div>)
    }
}

export default CSVData